import Controller from '../Controller.js';
import React from 'react';

import './Repo.css';

const Item = (props) =>
      <li className="RepoItem" onClick={props.onSelect}>
      <h2>{props.title}</h2>
      <img src={props.banner} alt={props.title}/>
      </li>;



export default class Repo extends Controller {

    constructor( props ){
	super( props );
	this.state = {repo:props.repo};
    }

    componentWillReceiveProps( props ){
	this.setState( state => ({repo: props.repo}));
	this.message("onViewRepo", props.repo.id);	
    }
    
    componentDidMount(){
	super.componentDidMount();
	this.message("onViewRepo", this.props.repo.id);
    }

    onUpdateRepo( repo ){
	this.setState( state => {
	    if( state.repo.id == repo.id )
		return {repo};
	    return null;
	});
    }

    title( props ){
	return <h1>{props.repo.title}</h1>;
    }

    items( props ){
	return <ul className="market">
	    {props.repo.items.map( (item,i) => (
		<Item key={i} onSelect={ _ => this.message("onSelectGame", item) } {...item}/>
	    ))}
	</ul>;
    }

    empty( repo ){
	return <div>This repo is empty!</div>;
    }

    loading(){
	return <div>Loading...</div>;
    }
    
    render(){
	console.log("Rendering repo ", this.state.id);
	return (
	    <div id="content">
	      {this.title( this.state )}
	      {
		  !this.state.repo.items ? this.loading() : (
		      !this.state.repo.items.length ? this.empty() :
			  this.items( this.state )
		  )
	      }
	    </div>
	);
    }
}




