export default class System {

    registers={

	0xE000E008:{
	    name:'ACTLR',
	    read:true,
	    write:true,
	    reset:0
	}, // IMPLEMENTATION DEFINED The Auxiliary Control Register, ACTLR on page B3-236

	0xE000E100:{
	    name:'ISER0'
	}, // Interrupt Set Enabled Register

	0xE000ED00:{
	    name:'CPUID',
	    read:true,
	    write:false,
	    reset:0
	}, // IMPLEMENTATION DEFINED CPUID Base Register

	0xE000ED04:{
	    name:'ICSR',
	    read:true,
	    write:true,
	    reset:0x00000000
	}, // 0x00000000 Interrupt Control State Register, ICSR on page B3-229

	0xE000ED08:{
	    name:'VTOR',
	    read:true,
	    write:true,
	    reset:0x00000000
	}, // 0x00000000 a Vector Table Offset Register, VTOR on page B3-231

	0xE000ED0C:{
	    name:'AIRCR',
	    read:true,
	    write:true,
	    reset:0
	}, // bits [10:8] = 0b000 Application Interrupt and Reset Control Register, AIRCR on page B3-232

	0xE000ED10:{
	    name:'SCR',
	    read:true,
	    write:true,
	    reset:0
	}, // bits [4,2,1] = 0b000 Optional System Control Register, SCR on page B3-233

	0xE000ED14:{
	    name:'CCR',
	    read:true,
	    write:false,
	    reset:~0
	}, // bits [9:3] = 0b1111111 Configuration and Control Register, CCR on page B3-234

	0xE000ED1C:{
	    name:'SHPR2',
	    read:true,
	    write:true,
	    reset:0
	}, // SBZ b System Handler Priority Register 2, SHPR2 onpage B3-235

	0xE000ED20:{
	    name:'SHPR3',
	    read:true,
	    write:true,
	    reset:0
	}, // SBZ c System Handler Priority Register 3, SHPR3 onpage B3-235

	0xE000ED24:{
	    name:'SHCSR',
	    read:true,
	    write:true,
	    reset:0x00000000
	}, // 0x00000000 System Handler Control and State Register, SHCSR on page C1-286 d

	0xE000ED30:{
	    name:'DFSR',
	    read:true,
	    write:true,
	    reset:0x00000000
	} // 0x00000000 Debug Fault Status Register, DFSR on page C1-286 d
	
    };

};



