import teamarg from './teamARG.json';
import pool from '../libs/Pool.js';

const defaults = {
    teamarg    
};

class BuiltinRepo {

    constructor(){
	pool.call("normalizeRepo", defaults.teamarg);
    }
    
    pollRepos( repos ){
	
	repos.push( ... Object.keys(defaults).map( k => Object.assign(
	    {id:k, title:defaults[k].title || defaults[k].repository},
	    defaults[k]
	) ) );
	
    }

    onDropFile( event ){
	var dt = event.dataTransfer;
	var files = dt.files;

	Array.from(files).forEach( file => {
	    let fr = new FileReader();
	    fr.onload = onLoad.bind(this, file, fr);
	    fr.readAsArrayBuffer(file);
	});

	function onLoad( file, fr ){
	    let parts = file.name.match(/([^/\\]+)\.([^/\\]+)$/);
	    if( !parts )
		return;

	    let ext = parts[2].toUpperCase();
	    let name = parts[1];

	    switch( ext ){
	    case "HEX":
		pool.call("onSelectGame", {
		    raw: fr.result,
		    rawType: "application/x-hex",
		    title: name,
		    device: "arduboy"
		});
		break;

	    case "ARDUBOY":
		pool.call("onSelectGame", {
		    raw:fr.result,
		    rawType:"application/x-arduboy",
		    title:name,
		    device:"arduboy"
		});
		break;
		
	    case "ZIP":
		pool.call("loadZipRepo", fr.result).then( repo => {
		    if( !repo || !repo.length )
			return;
		    pool.call("onSelectGame", repo[0]);
		});
		break;

	    case "BIN":
		pool.call("onSelectGame", {
		    raw:fr.result,
		    rawType:"application/x-bin",
		    title:name,
		    device:"pokitto"
		});
		break;
		
	    }
	}
	
    }
    
}

export default BuiltinRepo;



