function port( obj ){
    
    let out = { write:{}, read:{}, init:null };

    for( let k in obj ){
	
	let addr = obj[k];
	if( /DDR.|PORT./.test(k) ){
	    
	    out.write[ addr ] = setter(k);
	    
	}else{

	    out.read[ addr ] = getter(k);
	    out.init = init(k);
	    
	}
	
    }

    function setter( k ){
	return function( value, oldValue ){
	    if( value != oldValue )
		this.core.pins[k] = value;	    
	};
    }

    function getter( k ){
	return function(){
	    return (this[k] & 0xFF) | 0;
	};
    }

    function init( k ){
	return function(){
	    this[k] = 0;
	    let _this = this;
	    Object.defineProperty( this.core.pins, k, {
		set:function(v){ return _this[k] = (v>>>0) & 0xFF },
		get:function( ){ return _this[k] }
	    });
	}
    }
    
    return out;
    
}

module.exports = {

    PORTB:port({ PINB:0x23, DDRB:0x24, PORTB:0x25 }),
    PORTC:port({ PINC:0x26, DDRC:0x27, PORTC:0x28 }),
    PORTD:port({ PIND:0x29, DDRD:0x2A, PORTD:0x2B }),
//    PORTE:port({ PINE:0x2C, DDRE:0x2D, PORTE:0x2E }),
//    PORTF:port({ PINF:0x2F, DDRF:0x30, PORTF:0x31 }),

    TC0:require('./Timer8.js')({
	TIFR:  0x35,
	TCCR_A:0x44,
	TCCR_B:0x45,
	OCR_A: 0x47,
	OCR_B: 0x48,
	TIMSK: 0x6E,
	TCNT:  0x46,
	intOV: "TIMER0O"
    }),
/* */
    TC1:require('./Timer16.js')({
	TIFR:  0x36,
	TCCR_A:0x80,
	TCCR_B:0x81,
	TCCR_C:0x82,
	OCR_AH: 0x89,
	OCR_AL: 0x88,
	OCR_BH: 0x8B,
	OCR_BL: 0x8A,
	OCR_CH: 0x8D,
	OCR_CL: 0x8C,
	TIMSK: 0x6F,
	TCNTH: 0x85,
	TCNTL: 0x84,
	intOV: "TIMER1O",
	intCOMPA: "TIMER1A",
	intCOMPB: "TIMER1B",
	intCOMPC: "TIMER1C"
    }),
/* */
    TC3:require('./Timer16.js')({
	TIFR:  0x38,
	TCCR_A:0x90,
	TCCR_B:0x91,
	TCCR_C:0x92,
	TCNTH: 0x94,
	TCNTL: 0x95,
	OCR_AH: 0x99,
	OCR_AL: 0x98,
	OCR_BH: 0x9B,
	OCR_BL: 0x9A,
	OCR_CH: 0x9D,
	OCR_CL: 0x9C,
	TIMSK: 0x71,
	intOV: "TIMER3O",
	intCOMPA: "TIMER3A",
	intCOMPB: "TIMER3B",
	intCOMPC: "TIMER3C"
    }),

    USART:require('./At328P-USART.js'),

    PLL:{
	read:{
	    0x49:function( value ){
		return (this.PINDIV << 4) | (this.PLLE << 1) | this.PLOCK;
	    }
	},
	write:{
	    0x49:function( value, oldValue ){
		if( value === oldValue ) return;
		this.PINDIV = (value >> 4) & 1;
		this.PLLE   = (value >> 1) & 1;
		this.PLOCK  = 1;
	    }
	},
	init:function(){
	    this.PINDIV = 0;
	    this.PLLE = 0;
	    this.PLOCK = 0;
	}
    },

    SPI:require('./At32u4-SPI.js'),

    EEPROM:{
	write:{
	    0x3F:function( value, oldValue ){
		let addr = this.core.memory[0x41] + (this.core.memory[0x42] << 8);
		
		if( value & 0x2 )
		    this.eeprom[ addr ] = this.core.memory[0x40];

		value &= ~2;
		return value;
	    }
	},
	read:{
	    0x40:function(){
		let addr = this.core.memory[0x41] + (this.core.memory[0x42] << 8);
		return this.eeprom[ addr ] | 0;
	    }
	},
	init:function(){
	    this.eeprom = new Uint8Array( 1024 );
	    this.eeprom.fill(0xFF);
	    if( typeof window !== "undefined" )
		window.EEPROM = this.eeprom;
	}
    },

    ADC:require('./ADC.js')({
	ADCSRA: 0x7A,
	ADCH: 0x79,
	ADCL: 0x78,
	ADMUX: 0x7C,
	MUX:{
	    0:{ port:"PORTC", pin:0 },
	    1:{ port:"PORTC", pin:1 },
	    2:{ port:"PORTC", pin:2 },
	    3:{ port:"PORTC", pin:3 },
	    4:{ port:"PORTC", pin:4 },
	    5:{ port:"PORTC", pin:5 },
	    6:{ port:"PORTC", pin:8 },
	    7:{ port:"PORTC", pin:9 }
	}
    })

};

