let Binaryen = window.Binaryen;
let WebAssembly = window.WebAssembly;
const LOG_CPU = 0;
let blockCount = 0;
let logEnabled = false;
// let step = false;
window.step = false;

const DEBUG_BLOCK = {
    // [0x56b0 & 0xFC00]:true,
    // [0x4B30 & 0xFC00]:true
    // 0x1000:true
};

const BREAKPOINT = {
    0x4b30:true
};

let aborts = false, setsPC = false, constPC = 0, getsPC = false, debug = false;

let breakpoint = new Function(`
// Hello! You hit a breakpoint!
debugger;
`);

function getLocal( m, obj ){
    obj.i32 = v => (
	v === undefined ?	    
	    m.get_local(obj.local) :
	    m.set_local(obj.local, v)
    );

    return Object.assign( obj.i32, obj );
}

function getMap( m, obj, data ){
    
    obj.i32 = (off, v) => {
	if( v === undefined ){
	    
	    if( obj === data.reg._obj && off === 15 ){
		// getsPC = true;
		return data.i32(constPC);
	    }
	    
	    return m.i32.load(
		off*4,
		4,
		data.i32(obj.map.addr)
	    );
	    
	}else{
	    
	    if( obj === data.reg._obj && off === 15 )
		setsPC = true;
	    
	    return m.i32.store(
		off*4,
		4,
		data.i32(obj.map.addr),
		v
	    );
	}
    };

    obj.u8 = (off, v) => {
	if( v === undefined ){
	    
	    return m.i32.load8_u(
		off,
		0,
		data.i32(obj.map.addr)
	    );
	    
	}else{
	    
	    if( obj === data.reg._obj && (off>>2) === 15 )
		setsPC = true;
	    

	    return m.i32.store8(
		off,
		0,
		data.i32(obj.map.addr),
		v
	    );
	}
    };
	    
    obj.i8 = (off, v) => {
	if( v === undefined ){
	    
	    return m.i32.load8_s(
		off,
		0,
		data.i32(obj.map.addr)
	    );
	    
	}else{
	    
	    if( obj === data.reg._obj && (off>>2) === 15 )
		setsPC = true;
	    
	    return m.i32.store8(
		off,
		0,
		data.i32(obj.map.addr),
		v
	    );
	}
    };
    
    return Object.assign( obj.i32, obj );
    
}

function getData( core, m ){
    let d;
    let data = {
	quota:{
	    map:core.map.flags
	},
	result:{
	    local:1
	},
	carry:{
	    local:2
	},
	abort:{
	    local:3
	},
	oldQuota:{
	    local:4
	},
	reg:{
	    map:core.map.reg
	},
	flags:{
	    map:core.map.flags,
	    N: op => d.flags.u8( 0, op ),
	    Z: op => d.flags.u8( 1, op ),
	    C: op => d.flags.u8( 2, op ),
	    V: op => d.flags.u8( 3, op ),
	    IE: op => d.flags.u8( 5, op )
	},
	i32: v => m.i32.const(v),

	flash32: ( addr ) => {
	    if( debug )
		return d.memory32( addr );

	    return m.i32.load( 0, 0, m.i32.add(addr, d(core.map.flash.addr-core.map.flash.base)) );
	},

	sram32: ( addr, value ) => {
	    if( debug )
		return d.memory32( addr, value );

	    if( value === undefined )
		return m.i32.load( 0, 0, m.i32.add(addr, d(core.map.sram.addr-core.map.sram.base)) );
	    return m.i32.store( 0, 0, m.i32.add(addr, d(core.map.sram.addr-core.map.sram.base)), value );
	},
	
	memory32: ( addr, value ) => {
	    getsPC = true;
	    if( value === undefined )
		return m.callImport("readU32", [addr], Binaryen.i32);
	    return m.callImport("writeU32", [addr, value], Binaryen.none);
	},
	memory16: ( addr, value ) => {
	    getsPC = true;
	    if( value === undefined )
		return m.callImport("readU16", [addr], Binaryen.i32);
	    aborts = true;
	    return m.callImport("writeU16", [addr, value], Binaryen.none);
	},
	memory8: ( addr, value ) => {
	    getsPC = true;
	    if( value === undefined )
		return m.callImport("readU8", [addr], Binaryen.i32);
	    aborts = true;
	    return m.callImport("writeU8", [addr, value], Binaryen.none);
	}
	
    };

    for( let k in data ){

	let obj = data[k];
	
	if( data[k].local !== undefined )
	    data[k] = getLocal( m, obj );
	else if( data[k].map !== undefined )
	    data[k] = getMap( m, obj, data );

	data[k]._obj = obj;
	
    }

    d = Object.assign(
	c => data.i32(c),
	data
    );

    d.push = op => m.block(null, [
	d.reg( 13, m.i32.sub( d.reg(13), d(4) ) ),
	/* * / d.memory32( d.reg(13), op ) /*/
	m.i32.store( 0, 0, m.i32.add( d.reg(13), d(core.map.sram.addr-core.map.sram.base)), op )
	/* */
    ]);

    d.pop = _ => m.block(null, [
	d.result(
	    /* * / d.memory32( d.reg(13) ) /*/
	    m.i32.load( 0, 0, m.i32.add( d.reg(13), d(core.map.sram.addr-core.map.sram.base)) )
	    /* */
	),
	d.reg( 13, m.i32.add( d.reg(13), d(4) ) ),
	d.result()
    ], Binaryen.i32);

    return d;
}

let flags = {
    N(m, d){
	let p = m.i32.shr_u( d.result(), d(31) );
	return d.flags.N( p );
    },
    Z(m, d){
	let p = m.i32.eqz( d.result() );
	return d.flags.Z( p );
    },
    C(m, d){
	let p = d.carry();
	return d.flags.C( p );
    }
};

translate.blockSize = 1024;
translate.blockBits = 10;

export default function translate( core, PC ){
    console.log("Block count: " + (blockCount++) + ` #${PC.toString(16)} to #${(PC+2048).toString(16)}`);
    
    if( DEBUG_BLOCK[PC]&4 )
	debugger;
    // console.log("DEC: NEW BLOCK #" + PC.toString(16));
    
    let m = new Binaryen.Module();
    let data = getData( core, m );
 
    let vv = m.addFunctionType('vv', Binaryen.none, []);
    let iv = m.addFunctionType('iv', Binaryen.none, [Binaryen.i32]);
    let ii = m.addFunctionType('ii', Binaryen.i32, [Binaryen.i32]);
    // let iii = m.addFunctionType('iii', Binaryen.i32, [Binaryen.i32,Binaryen.i32]);
    let iiv = m.addFunctionType('iiv', Binaryen.none, [Binaryen.i32,Binaryen.i32]);
    let size = translate.blockSize;
    m.addMemoryImport("0", "env", "memory");
    m.addFunctionImport("log", "env", "log", iv);
    m.addFunctionImport("readU32", "env", "readU32", ii);
    m.addFunctionImport("writeU32", "env", "writeU32", iiv);
    m.addFunctionImport("readU16", "env", "readU16", ii);
    m.addFunctionImport("writeU16", "env", "writeU16", iiv);
    m.addFunctionImport("readU8", "env", "readU8", ii);
    m.addFunctionImport("writeU8", "env", "writeU8", iiv);
    m.addFunctionImport("updateHardware", "env", "updateHardware", vv);

    
    let opcode = null, ast;
    let NOP = {
	emit: m =>[],
	size:2
    };

    let addrs = [];
    let ops = [];

    let bitsA, bitsB = core.readU16( PC, true );
    debug =  DEBUG_BLOCK[PC];
    
    for( let i=0; i<size; i += 2 ){

	bitsA = bitsB;
	bitsB = core.readU16( PC+i+2, true );
	
	opcode = core.decoder.decode( bitsA | (bitsB<<16), PC+i, debug );

	let warn = !opcode;
	
	if( !opcode ){
	    /* * /
	    console.warn(
		"DEC: ",
		"Unknown op: ",
		'#'+(PC+i).toString(16).padStart(8, '0'),
		bitsA.toString(16).padStart(4, '0'),
		bitsA.toString(2).padStart(16, '0')
	    );
	    /* */
	}else if( false ){
	    console.log(
		"DEC: ",
		opcode.toString()
	    );
	}

	opcode = opcode || NOP;

	getsPC = false;
	setsPC = false;
	aborts = false;
	constPC = (PC+i+4) & ~1;

	addrs.push( (PC+i)+'' );
	
	ast = opcode.emit( m, data );
	
	if( !Array.isArray(ast) )
	    ast = [ast];

	ops.push( ast );

	if( opcode.flags && opcode.setflags !== false ){
	    for( let flag in opcode.flags ){
		let em = opcode.flags[flag];
		if( em === true )
		    em = flags[flag];
		let emast = em( m, data );
		if( !Array.isArray(emast) )
		    emast = [emast];
		ast.push( ...emast );
	    }
	}

	if( aborts ){
	    ast.push(
		m.if(
		    data.abort(),
		    m.block(null, [
			m.i32.store( 15*4, 4, data.i32(core.map.reg.addr), data.i32(constPC) ),
			m.return(data.i32(0))
		    ])
		)
	    );
	}

	/* */
	
	if( LOG_CPU || warn || debug )
	    ast.unshift( m.callImport("log", [data(PC+i)], iv) );

	if( opcode.size>2 )
	    setsPC = true;

	if( setsPC )
	    ast.push( m.br('loop') );
	
	if( setsPC || getsPC )
	    ast.unshift(
		m.i32.store( 15*4, 4, data.i32(core.map.reg.addr), data.i32((PC+i+opcode.size)&~1) )
		// data.reg( 15, data(PC+i+2) )
	    );

	// if( opcode.size == 4 ){
	//     addrs.push( (PC+i+2)+'' );
	//     ops.push( [m.nop()] );
	//     bitsB = core.readU16( PC+i+4, true );
	// }
	/* */
    }

    /* */
    if( !setsPC )
	ast.push(
	    data.reg(
		15,
		data((addrs[addrs.length-1]|0)+opcode.size)
	    )
	);
    /* */

    let s = [
	m.block( 'default', [
	    m.switch(
		addrs,
		"default",
		m.i32.shr_u(
		    m.i32.sub( m.i32.load(
			15*4,
			4,
			data.i32(core.map.reg.addr)
		    ), data(PC) ),
		    data(1)
		)
	    )
	]),
	m.return( data(0) )
    ];

    for( let i=0; i<ops.length; ++i ){
	try{
	    s = [
		m.block( addrs[i], s ),

		data.quota(
		    2,
		    m.i32.sub(
			data.quota(2),
			data(1)
		    )
		),
		
		... ops[i]
		
	    ];
	}catch( ex ){
	    // console.error(ex);
	    console.error("Error in address: #" + (addrs[i-1]|0).toString(16));
	    debugger;
	}
    }
    
    let L = m.block(null, [
	data.oldQuota( data.quota(2) ),
	m.loop(
	    'loop',
	    m.block(null, [
		
		m.if(
		    m.i32.le_s(
			data.quota(2),
			data(0)
		    ),
		    m.return( data(0) )
		),

		m.if(
		    m.i32.gt_u(m.i32.sub( data.oldQuota(), data.quota(2) ), data(5) ),
		    m.block(null, [
			data.oldQuota(data.quota(2)),
			m.callImport("updateHardware", [], vv)
		    ])
		), 
		
		m.block(null, s),
		
		m.br('loop')
		
	    ])
	)
    ]);

    m.addFunction('exec', ii, [Binaryen.i32, Binaryen.i32, Binaryen.i32, Binaryen.i32], L);
    m.addFunctionExport('exec', 'exec');

    // console.log( m.emitText() );
    
    // m.optimize();

    let binary = m.emitBinary();
    m.dispose();


    return WebAssembly
	.compile( binary )
	.then( module => WebAssembly.instantiate( module, {
	    env:{
		log: x => {
		    logEnabled = logEnabled || BREAKPOINT[x];

		    let low = core.readU16( x, true );
		    let op = core.decoder.decode(
			low + (core.readU16( x+2, true )<<16), x
		    );
		    
		    if( !logEnabled && op ) return;

		    let style = "";
		    if( BREAKPOINT[x] )
			style = "background: #345; color: #abc";

		    let flags = "NZCVI".split("");
		    for( let i=0; i<flags.length; ++i ){
			if( !core.map.flags.uint8[i] )
			    flags[i] = flags[i].toLowerCase();
			else if( core.map.flags.uint8[i] !== 1 )
			    flags[i] += core.map.flags.uint8[i];
		    }
		    flags = flags.join("");

		    let str = op ? op.toString(core) : "#" + x.toString(16) + ": UNKNOWN ";
		    
		    if( !op ){
			core.dead = true;
			console.error("%cCPU: " + str + low.toString(2).padStart(16, '0'), "background: red; color: white");
		    }else
			console.log("%cCPU: " + str + " [" + flags + "]", style);

		    if( BREAKPOINT[x] ||
			(typeof window.step=="function" && window.step(str)) ||
			(typeof window.step!="function" && window.step)
		      )
			breakpoint();

		},
		memory: core.memory,
		readU32: core.readU32,
		writeU32: core.writeU32,
		readU16: core.readU16,
		writeU16: core.writeU16,
		readU8: core.readU8,
		writeU8: core.writeU8,
		updateHardware: core.updateHardware
	    }
	}) )
	.then( inst => Object.assign(inst.exports.exec, {debug:DEBUG_BLOCK[PC]}) );

}




