export default function parseInf( name, inf, hex, type ){
    let ret = {
	title:name,
	binaries:[{
	    raw:hex,
	    rawType:type,
	    device:'Gamebuino'
	}]
    };
    
    let pos = 0;
	    
    if( 
	[0x67,0x62,0x69,0x6e,0x66,0x00,0x01]
	    .find( ch => ch != inf[pos++] ) === undefined
    ){
	ret.title = "";
	while(inf[pos])
	    ret.title += String.fromCharCode(inf[pos++]);

	pos = 19+7;

	ret.banner = parseImage(19,18);

	let nos = inf[pos++];
		
	if( nos )
	    ret.banner = parseImage(84,32);
    }


    function parseImage( w, h ){
	let c = document.createElement("canvas");
	c.width = w;
	c.height = h;
	let ctx = c.getContext("2d");
	let img = ctx.createImageData(w,h);
	for( let y=0; y<h; ++y ){
	    for( let x=0; x<w; x+=8 ){
		let d = inf[pos++];
		for( let i=0; i<8; ++i ){
		    let p = (d>>(7-i)) & 1, a = (y*w + x + i)*4;
		    p *= 0xFF;
		    img.data[ a++ ] = p;
		    img.data[ a++ ] = p;
		    img.data[ a++ ] = p;
		    img.data[ a++ ] = 0xFF;
		}
	    }
	}
	ctx.putImageData( img, 0, 0 );
	return c.toDataURL();
    }
    
    return ret;
}



