import pool from '../libs/Pool.js';

const defaults = {
    eried:{
	title:"Erwin's Arduboy Collection",
	url:"http://arduboy.ried.cl/repo.json"
    },
    crait:{
	title:"Crait's Arduboy Repo",
	url:"http://www.crait.net/arduboy/repo2.json"
    },
    /*
    gamebuino:{
	title:"Gamebuino Classic",
	url:"https://github.com/Rodot/Gamebuino-Games-Compilation/archive/master.zip"
    },
    */
    makerbuino:{
	title:"Makerbuino Creations",
	url:"http://www.makerbuino.com/wp-content/uploads/2017/11/MAKERbuino-SD-game-compilation.zip"
    }
};

class OnlineRepo {

    constructor( proxy ){
	this.proxy = proxy||"";
    }

    loadGame( game, system, cb ){
	
	let url = game.url || (/^https?:/i.test(game.filename) && game.filename);
	if( !url || game.raw || game.localfile )
	    return;

	if( /^https?:/i.test(url) )
	    url = this.proxy+decodeURIComponent(url);

	fetch( url )
	    .then( rsp => {
		game.rawType = (rsp.headers.get("content-type")||"").toLowerCase();
		if( !/text|utf-8/.test(game.rawType) )
		    return rsp.arrayBuffer();
		return rsp.text();
	    })
	    .then( ret => {
		game.raw = ret;

		if( ret instanceof ArrayBuffer ){
		    let buff = new Uint8Array(game.raw);
		    if( buff[0] != 'P'.charCodeAt(0) || buff[1] != 'K'.charCodeAt(0) )
			game.rawType = 'application/x-bin';
		}
		
		pool.call("loadGame", game, system, cb);
		return;
	    })
	    .catch( ex => cb( ex+"" ) );
    }
    
    pollRepos( repos ){
	
	repos.push( ... Object.keys(defaults).map( k => Object.assign(
	    {id:k, title:defaults[k].title || defaults[k].repository},
	    defaults[k]
	) ) );
	
    }

    onViewRepo( id ){
	let repo = defaults[id];

	if(!repo)
	    return;

	let age = (new Date()) - (repo.timestamp||0);
	
	if( age > 4*60*60*1000 )
	    this.refresh(id);
	else
	    pool.call("onUpdateRepo", Object.assign({id}, defaults[id]));
    }

    refresh( id ){
	let repo = defaults[id];
	repo.timestamp = +(new Date());
	console.log("Refreshing ", repo.title);

	if( repo.url )
	    fetch( this.proxy+repo.url )
	    .then( rsp => {
		let type = (rsp.headers.get("content-type")||"").toLowerCase();
		if( !/text|json|utf-8/.test(type) )
		    return rsp.arrayBuffer();
		if( /html/.test(type) )
		    return rsp.text();
		return rsp.json();
	    })
	    .then( obj => {

		if( typeof obj == "string" ){
		    let dom = (new window.DOMParser()).parseFromString(obj, "text/html");
		    console.log( dom );
		}else if( obj instanceof ArrayBuffer )
		    pool.call("loadZipRepo", obj ).then( items => {
			repo.items = items;
			pool.call("onUpdateRepo", Object.assign({id}, repo));			
		    });
		else{
		    pool.call("normalizeRepo", obj);
		    Object.assign(repo, obj);
		    pool.call("onUpdateRepo", Object.assign({id}, repo));
		}
		
	    });
    }

}

export default OnlineRepo;



