class Normalizer {
    normalizeRepo( repo ){
	if( !repo.items )
	    repo.items = [];
	repo.items.forEach( item => {

	    if( !Array.isArray(item.binaries) )
		item.binaries = [];
	    if( item.arduboy ){
		item.binaries.push( {
		    filename: item.arduboy,
		    device:"Arduboy"
		} );
	    }
	    
	    if( item.screenshots && item.screenshots.length && item.screenshots[0] && item.screenshots[0].filename )
		item.banner = item.screenshots[0].filename;

	    item.binaries.forEach( bin => bin.device = (bin.device||"").toLowerCase() );

	});
    }
}

export default Normalizer;



