export default class Const {

    init( id, obj ){
	this.value = obj.value;
    }

    pins = {

	D:{
	    init:function( obj ){
		this.pins.D.value = obj.value | 0;
	    }
	},

	A:{
	    onReadADC:function(){
		return Math.floor(this.value*1024)|0;
	    }
	}
	
    }
    
}




