import Atcore from '../atcore/Atcore.js';
import pool from '../libs/Pool.js';

const INIT    =-1;
const LOADING = 0;
const RUNNING = 1;
const PAUSED  = 2;
const STEP    = 3;

export default class At32u4 {

    constructor(){
	this.dead = false;
	this.state = -1;
	this.initCore();
    }

    reset(){
	// prevent from running without a hex
	if( this.state == LOADING )
	    return;

	this.core.reset();

    }

    resume(){
	if( this.state == PAUSED )
	    this.core.breakpoints.disableFirst = true;
	if( this.state != LOADING )
	    this.state = RUNNING;
    }

    step(){
	if( this.state == PAUSED ){
	    this.state = STEP;
	    this.core.breakpoints.disableFirst = true;
	}
    }

    pause(){
	if( this.state != LOADING )
	    this.state = PAUSED;
    }

    _instanceCore(){
	this.core = Atcore.ATmega32u4();
    }

    initCore( preserve ){
	this._instanceCore();
	
	let core = this.core,
	    oldValues = {},
	    callbacks = {
		DDRB:{},
		DDRC:{},
		DDRD:{},
		PORTB:{},
		PORTC:{},
		PORTD:{},
		PORTE:{},
		PORTF:{}
	    };
	
	Object.keys(callbacks).forEach( k =>
					Object.assign(callbacks[k],{
					    onHighToLow:[], 
					    onLowToHigh:[],
					    onChangePWM:[],
					    onReadADC:[]
					})
				      );

	Object.defineProperties( core.pins, {

            onHighToLow:{value:function( port, bit, cb ){
		(callbacks[ port ].onHighToLow[ bit ] = callbacks[ port ][ bit ] || []).push( cb );
            }},

            onLowToHigh:{value:function( port, bit, cb ){
		(callbacks[ port ].onLowToHigh[ bit ] = callbacks[ port ][ bit ] || []).push( cb );
            }},

            onChangePWM:{value:function( port, bit, cb ){
		(callbacks[ port ].onChangePWM[ bit ] = callbacks[ port ][ bit ] || []).push( cb );
            }},

	    setPWM:{ value:function( port, bit, value ){
		let arr = callbacks[ port ].onChangePWM;
		for( let i=0; i<arr.length; ++i )
		    arr[i]( value, core.tick );
	    }},

            onReadADC:{value:function( port, bit, cb ){
		callbacks[ port ].onReadADC[ bit ] = cb;
            }},

	    readADC:{value:function( port, bit ){
		let func = callbacks[ port ].onReadADC[ bit ];
		if( !func ){
		    console.log( "Reading floater ", port, bit );
		    func = callbacks[ port ].onReadADC[ bit ] = _ => Math.floor( Math.random()*1024 );
		}
		return func() | 0;
	    }},

            0:{value:{ out:{port:"PORTD", bit:2 }, in:{port:"PIND", bit:2} } },
            PORTD2 :{value:{ out:{port:"PORTD", bit:2 }, in:{port:"PIND", bit:2} } },
            1:{value:{ out:{port:"PORTD", bit:3 }, in:{port:"PIND", bit:3} } },
            PORTD3 :{value:{ out:{port:"PORTD", bit:3 }, in:{port:"PIND", bit:3} } },
            2:{value:{ out:{port:"PORTD", bit:1 }, in:{port:"PIND", bit:1} } },
            PORTD1 :{value:{ out:{port:"PORTD", bit:1 }, in:{port:"PIND", bit:1} } },
            3:{value:{ out:{port:"PORTD", bit:0 }, in:{port:"PIND", bit:0} } },
            PORTD0 :{value:{ out:{port:"PORTD", bit:0 }, in:{port:"PIND", bit:0} } },
            4:{value:{ out:{port:"PORTD", bit:4 }, in:{port:"PIND", bit:4} } },
            PORTD4 :{value:{ out:{port:"PORTD", bit:4 }, in:{port:"PIND", bit:4} } },
            30:{value:{ out:{port:"PORTD", bit:5 }, in:{port:"PIND", bit:5} } },
            PORTD5 :{value:{ out:{port:"PORTD", bit:5 }, in:{port:"PIND", bit:5} } },
            PORTD6 :{value:{ out:{port:"PORTD", bit:6 }, in:{port:"PIND", bit:6} } },
	    
            5:{value:{ out:{port:"PORTC", bit:6 }, in:{port:"PINC", bit:6} } },
            PORTC0 :{value:{ out:{port:"PORTC", bit:0 }, in:{port:"PINC", bit:0} } },
            PORTC1 :{value:{ out:{port:"PORTC", bit:1 }, in:{port:"PINC", bit:1} } },
            PORTC2 :{value:{ out:{port:"PORTC", bit:2 }, in:{port:"PINC", bit:2} } },
            PORTC3 :{value:{ out:{port:"PORTC", bit:3 }, in:{port:"PINC", bit:3} } },
            PORTC6 :{value:{ out:{port:"PORTC", bit:6 }, in:{port:"PINC", bit:6} } },
	    13:{value:{ out:{port:"PORTC", bit:7 }, in:{port:"PINC", bit:7} } },	    
	    PORTC7 :{value:{ out:{port:"PORTC", bit:7 }, in:{port:"PINC", bit:7} } },	    
            PORTC8 :{value:{ out:{port:"PORTC", bit:8 }, in:{port:"PINC", bit:8} } },
            PORTC9 :{value:{ out:{port:"PORTC", bit:9 }, in:{port:"PINC", bit:9} } },

            6:{value:{ out:{port:"PORTD", bit:7 }, in:{port:"PIND", bit:7} } },
            PORTD7 :{value:{ out:{port:"PORTD", bit:7 }, in:{port:"PIND", bit:7} } },
            7:{value:{ out:{port:"PORTE", bit:6 }, in:{port:"PINE", bit:6} } },
            PORTE6 :{value:{ out:{port:"PORTE", bit:6 }, in:{port:"PINE", bit:6} } },
            8:{value:{ out:{port:"PORTB", bit:4 }, in:{port:"PINB", bit:4} } },
            PORTB4 :{value:{ out:{port:"PORTB", bit:4 }, in:{port:"PINB", bit:4} } },
            9:{value:{ out:{port:"PORTB", bit:5 }, in:{port:"PINB", bit:5} } },
            PORTB5 :{value:{ out:{port:"PORTB", bit:5 }, in:{port:"PINB", bit:5} } },
            10:{value:{ out:{port:"PORTB", bit:6 }, in:{port:"PINB", bit:6} } },
            PORTB6 :{value:{ out:{port:"PORTB", bit:6 }, in:{port:"PINB", bit:6} } },
            11:{value:{ out:{port:"PORTB", bit:7 }, in:{port:"PINB", bit:7} } },
            PORTB7 :{value:{ out:{port:"PORTB", bit:7 }, in:{port:"PINB", bit:7} } },

	    16:{value:{ out:{port:"PORTB", bit:2 }, in:{port:"PINB", bit:2} } },
	    PORTB2 :{value:{ out:{port:"PORTB", bit:2 }, in:{port:"PINB", bit:2} } },
            14:{value:{ out:{port:"PORTB", bit:3 }, in:{port:"PINB", bit:3} } },
            PORTB3 :{value:{ out:{port:"PORTB", bit:3 }, in:{port:"PINB", bit:3} } },
            15:{value:{ out:{port:"PORTB", bit:1 }, in:{port:"PINB", bit:1} } },
            PORTB1 :{value:{ out:{port:"PORTB", bit:1 }, in:{port:"PINB", bit:1} } },
            17:{value:{ out:{port:"PORTB", bit:0 }, in:{port:"PINB", bit:0} } },
            PORTB0 :{value:{ out:{port:"PORTB", bit:0 }, in:{port:"PINB", bit:0} } },

            18:{value:{ out:{port:"PORTF", bit:7 }, in:{port:"PINF", bit:7} } },
            PORTF7 :{value:{ out:{port:"PORTF", bit:7 }, in:{port:"PINF", bit:7} } },
            A0:{value:{ out:{port:"PORTF", bit:7 }, in:{port:"PINF", bit:7} } },
	    19:{value:{ out:{port:"PORTF", bit:6 }, in:{port:"PINF", bit:6} } },
	    PORTF6 :{value:{ out:{port:"PORTF", bit:6 }, in:{port:"PINF", bit:6} } },
	    A1:{value:{ out:{port:"PORTF", bit:6 }, in:{port:"PINF", bit:6} } },
	    20:{value:{ out:{port:"PORTF", bit:5 }, in:{port:"PINF", bit:5} } },
	    PORTF5 :{value:{ out:{port:"PORTF", bit:5 }, in:{port:"PINF", bit:5} } },
	    A2:{value:{ out:{port:"PORTF", bit:5 }, in:{port:"PINF", bit:5} } },
	    21:{value:{ out:{port:"PORTF", bit:4 }, in:{port:"PINF", bit:4} } },
	    PORTF4 :{value:{ out:{port:"PORTF", bit:4 }, in:{port:"PINF", bit:4} } },
	    A3:{value:{ out:{port:"PORTF", bit:4 }, in:{port:"PINF", bit:4} } },
	    
	    MOSI:{value:{}},
	    MISO:{value:{}},

	    spiIn:{
		value:[]
	    },
	    
	    spiOut:{
		value:{
		    listeners:[],
		    push( data ){
			let i=0, listeners=this.listeners, l=listeners.length;
			for(;i<l;++i)
			    listeners[i]( data );
		    }
		}
	    },

            serial0Out:{
		value:{
		    listeners:[],
		    push( data ){
			let i=0, listeners=this.listeners, l=listeners.length;
			for(;i<l;++i)
			    listeners[i]( data );
		    }
                    
		}
            },

            DDRB: {
		set: setDDR.bind(null, "DDRB"),
		get:function(){
                    return oldValues.DDRB|0;
		}
            },
            DDRC: {
		set: setDDR.bind(null, "DDRC")
            },
            DDRD: {
		set: setDDR.bind(null, "DDRD")
            },
            DDRE: {
		set: setDDR.bind(null, "DDRD")
            },
            DDRF: {
		set: setDDR.bind(null, "DDRD")
            },
            PORTB: {
		set: setPort.bind(null, "PORTB")
            },
            PORTC: {
		set: setPort.bind(null, "PORTC")
            },
            PORTD: {
		set: setPort.bind(null, "PORTD")
            },
            PORTE: {
		set: setPort.bind(null, "PORTE")
            },
            PORTF: {
		set: setPort.bind(null, "PORTF")
            }

	});

	function setDDR( name, cur ){   
            var old = oldValues[name];                    
            if( old === cur ) return;
            oldValues[name] = cur;
	}

	function setPort( name, cur ){
            var old = oldValues[name];
            
            if( old === cur ) return;
            var s, j, l, lth = callbacks[name].onLowToHigh, htl = callbacks[name].onHighToLow, tick = core.tick;

            for( var i=0; i<8; ++i ){

		var ob = old>>>i&1, nb = cur>>>i&1;
		if( lth[i] && !ob && nb ){
                    for( j=0, s=lth[i], l=s.length; j<l; ++j )
			s[j]( tick );
		}
		if( htl[i] && ob && !nb ){
                    for( j=0, s=htl[i], l=s.length; j<l; ++j )
			s[j]( tick );
		}

            }

	    oldValues[name] = cur;

	}
    }

    JITwarmup(){

	let core = this.core;
	let startPC = core.pc;
	core.pc = 0;

	while( core.pc < core.prog.length )
	    core.getBlock( false );

	core.pc = startPC;

    }

    bindPin( pin, part ){
	let ctrl = part.ctrl;
	let pins = this.core.pins;

	if( part.MOSI )
	    pins.spiOut.listeners.push( part.MOSI.bind( ctrl ) );

	if( part.serial0 )
	    pins.serial0Out.listeners.push( part.serial0.bind(ctrl) );

	let tobj = pins[pin];

	if( part.onLowToHigh )
	    pins.onLowToHigh(
		tobj.out.port,
		tobj.out.bit,
		part.onLowToHigh.bind( ctrl )
	    );
		
	if( part.onHighToLow )
	    pins.onHighToLow(
		tobj.out.port,
		tobj.out.bit,
		part.onHighToLow.bind( ctrl )
	    );

	if( part.onChangePWM )
	    pins.onChangePWM(
		tobj.out.port,
		tobj.out.bit,
		part.onChangePWM.bind( ctrl )
	    );

	if( part.onReadADC  )
	    pins.onReadADC (
		tobj.out.port,
		tobj.out.bit,
		part.onReadADC.bind( ctrl )
	    );
	
	let setter = (function( tobj, nv ){
		    
	    if( nv ) pins[ tobj.in.port ] |= 1 << tobj.in.bit;
	    else pins[ tobj.in.port ] &= ~(1 << tobj.in.bit);
		    
	}).bind(this, tobj);

	let getter = (function( tobj ){
	    return (pins[ tobj.out.port ] >>> tobj.out.bit) & 1;
	}).bind(this, tobj);

	Object.defineProperty(part, "value", {
	    set:setter,
	    get:getter
	});

	if( part.init )
	    part.init.call( ctrl, part );

    }

    tick(){
	if( this.dead ) return;
	
	switch( this.state ){
	case INIT:
	    this.JITwarmup();
	    this.state = RUNNING;
	    return;
	case RUNNING:
	    this.core.update();
	    break;
	case STEP:
	    this.core.exec( (this.core.tick-this.core.endTick+1) / this.core.clock);
	    this.state = PAUSED;
	    this.core.breakpointHit = true;
	    break;
	default:
	    return;
	}

	if( this.core.breakpointHit ){
	    this.core.breakpointHit = false;
	    this.state = PAUSED;
	    pool.call("onHitBreakpoint", this.core.pc);
	}
    }
    
}




