export default class DEBUG {
    registers = {
	0x21000000:{ name:"DEBUG", write:(v, oldv, core)=>{
	    var DBGEN = this.DBGEN;
	    if( !DBGEN )
		return v;
	    console.log( "%cDBG: #" + (core.PC-2).toString(16).padStart(8, '0') + ": " + v + " / #" + (v>>>0).toString(16) + " / " + (v>>>0).toString(2).padStart(32, '0') + "b", "background: #897;" );
	    if( DBGEN&2 )
		(function(){debugger;})();
	    if( DBGEN&4 ){
		if( !this.DBGCNT )
		    this.DBGEN = 0;
		else
		    this.DBGCNT--;
	    }
	    return v;
	}},
	0x21000004:{ name:"DBGEN" },
	0x21000008:{ name:"DBGCNT" }
    }
}



