module.exports = ADDR => ({

    init:function(){
	this.ADEN = 0;
	this.ADSC = 0;
	this.ADATE = 0;
	this.ADIF = 0;
	this.ADIE = 0;
	this.ADPS2 = 0;
	this.ADPS1 = 0;
	this.ADPS0 = 0;
	this.ADMUX = 0;
	this.MUX = 0;
	this.readADC = function(){
	    let obj = ADDR.MUX[ this.MUX ];
	    if( !obj ) return 0;
	    return this.core.pins.readADC( obj.port, obj.pin );
	};
    },

    update:function( tick, ie ){
	if( this.ADEN && this.ADIE ){
	    this.ADIF = 1;
	    this.ADSC = 0;
	    let v = this.readADC();
	    this.ADCH = v >>> 8;
	    this.ADCL = v & 0xFF;
	}

	if( this.ADIF && this.ADIE && ie ){
	    this.ADIF = 0;
	    return "ADC";
	}
    },
    
    read:{
	[ADDR.ADCH]:function(){
	    return this.ADCH;
	},
	[ADDR.ADCL]:function(){
	    return this.ADCL;
	}
    },

    write:{

	[ADDR.ADMUX]:function(value){
	    this.ADMUX = value;
	    this.MUX = value & 0x1F;
	},

	[ADDR.ADCSRA]:function(value, oldValue){
	    this.ADEN = value>>7 & 1;
	    this.ADSC = value>>6 & 1;
	    this.ADATE = value>>5 & 1;
	    this.ADIF = value>>4 & 1;
	    this.ADIE = value>>3 & 1;
	    this.ADPS2 = value>>2 & 1;
	    this.ADPS1 = value>>1 & 1;
	    this.ADPS0 = value & 1;
	    if( this.ADEN ){
		if( this.ADSC ){
		    let v = this.readADC();
		    this.ADCH = v >>> 8;
		    this.ADCL = v & 0xFF;
		    this.ADSC = 0;
		    value &= ~(1<<6);
		}
	    }
	    return value;
	}

    }
});



