/* global LOG_RAM */

import buildBlock from './buildBlock.js';
import Decoder from './Decoder.js';

import Thumb1 from './is/Thumb1.js';
import Thumb2 from './is/Thumb2.js';

import System from './devices/System.js';
import LPC11U6 from './devices/LPC11U6.js';
import LPC11U6_IO from './devices/LPC11U6_IO.js';
import CT32B1 from './devices/CT32B1.js';
import LPC11U6_SPI from './devices/LPC11U6_SPI.js';
import DEBUG from './devices/DEBUG.js';

// let LOG_RAM = 0;
window.LOG_RAM = 0;

let WebAssembly = window.WebAssembly;


export default class CPU {
    
    constructor(){

	window.CPU = this;

	this.tickCycles = 10000000;

	this.decoder = new Decoder([
	    Thumb1,
	    Thumb2
	]);
	this.wasInit = false;
	this.ready = false;
	this.dead = false;
	this.building = false;
	this.blocks = [];
	this.timers = [];
	this.timeSinceReboot = performance.now();
	this.cyclesExecuted = 0;
	this.ticksSincePrint = 0;
	this.pins = {};
	
	let devices = {
	    System: new System(),
	    LPC11U6: new LPC11U6(),
	    LPC11U6_IO: new LPC11U6_IO(),
	    CT32B1: new CT32B1(),
	    SPI0: new LPC11U6_SPI(0),
	    SPI1: new LPC11U6_SPI(1),
	    DEBUG: new DEBUG()
	};
	
	const map = {
	    reg:{ 
		length: 16<<2, base:-1
	    },
	    flags:{
		length: 3<<2, base:-1		
	    },
	    flash:{ 
		length: 0x40000, base:0, write:false
	    },
	    sram:{ 
		length:0x8000, base:0x10000000
	    },
	    
	    sram1:{ 
		length:0x800, base:0x20000000
	    },
	    
	    usbsram:{ 
		length:0x800, base:0x20004000
	    },

	    eeprom:{ 
		length:0x1000, base:-1
	    }
	    
	};

	this.map = map;

	this.devices = Object.keys(devices).map( deviceName => {
	    let device = devices[deviceName];
	    if( device.tick )
		this.timers.push( device );

	    for( let regName in device.registers ){
		map[ deviceName + "$" + device.registers[regName].name ] = {
		    name:deviceName,
		    device,
		    register:device.registers[regName],
		    base:regName>>>0,
		    length: 4
		};
	    }

	    return device;
	});
	
	let array = Object.keys(map)
	    .map( key =>{
		map[key].name = map[key].name || key;
		return map[key];
	    })
	    .sort( (a, b) => {
		if( a.base < b.base ) return -1;
		if( a.base > b.base ) return 1;
		return 0;
	    });	

	let totalMemory = 0;
	array.forEach( region => {
	    region.addr = totalMemory;
	    totalMemory += region.length;	   
	});
	
	this.memory = new WebAssembly.Memory({initial:Math.ceil(totalMemory/0x10000)});

	(new Uint8Array(this.memory.buffer)).fill(0xFF);

	let registers = {}, THIS = this;
	
	for( let regionName in map ){
	    let region = map[regionName];

	    region.uint8 = new Uint8Array(
		this.memory.buffer,
		region.addr,
		region.length
	    );
	    region.uint16 = new Uint16Array(
		this.memory.buffer,
		region.addr,
		region.length>>1
	    );
	    region.uint32 = new Uint32Array(
		this.memory.buffer,
		region.addr,
		region.length>>2
	    );	

	    region.int8 = new Int8Array(
		this.memory.buffer,
		region.addr,
		region.length
	    );
	    region.int16 = new Int16Array(
		this.memory.buffer,
		region.addr,
		region.length>>1
	    );
	    region.int32 = new Int32Array(
		this.memory.buffer,
		region.addr,
		region.length>>2
	    );

	    if( region.register ){

		if( region.register.read === true || region.register.read === undefined ){
		    // region.read = getReader( region.uint32 );
		}else if( typeof region.register.read == 'number' )
		    region.read = getConstReader( region.read );
		else if( !region.register.read )
		    region.read = getConstReader( 0 );
		else
		    region.read = getCBReader( region );
		
		if( region.register.write === true || region.register.write === undefined ){
		    // region.write = getWriter( region.uint32 );
		}else if( region.register.write )
		    region.write = getCBWriter( region );
		else
		    region.write = _ => {
			console.log( "#" + (this.PC-2).toString(16) + ": *** WRITE TO RO REGION ***");
		    };

		registers[ region.register.name ] = {
		    get:region.read || getReader( region.uint32 ),
		    set:region.write || getWriter( region.uint32 )
		};

		Object.defineProperty(
		    region.device,
		    region.register.name,
		    // registers[ region.register.name ]
		    {
			get:getReader( region.uint32 ),
			set:getWriter( region.uint32 )
		    }
		);

		region.uint32[0] = region.register.reset | 0;
		
	    }else if( region.write === false ){
		region.write = _ => {
		    console.log( "#" + (this.PC-2).toString(16) + ": *** WRITE TO RO REGION ***");
		};
	    }

	}

	this.reg = {};
	Object.defineProperties( this.reg, registers );

	while( array[0].base == -1 )
	    array.shift();

	map.array = array;
	this.flash = map.flash.uint8;

	map.sram.uint8.fill(0xFF);
	this.initMemAccess();

	this.instance = null;

	function getCBReader( region ){
	    return function(){
		return region.uint32[0] = region.register.read.call(
		    region.device,
		    region.uint32[0],
		    THIS
		);
	    };
	}

	function getCBWriter( region ){
	    return function( v ){
		return region.uint32[0] = region.register.write.call(
		    region.device,
		    v,
		    region.uint32[0],
		    THIS
		);
	    };
	}
	
	function getConstReader( v ){
	    return function(){
		return v;
	    };
	}

	function getReader( arr ){
	    return function(){
		return arr[0];
	    };
	}

	function getWriter( arr ){
	    return function( v ){
		return arr[0] = v;
	    };
	}
	
    }

    initMemAccess(){
	let arr = new Uint32Array(1);
	arr[0] = 1;
	let b = new Uint8Array( arr.buffer );
	if( b[0] ){
	    this.initLEMemAccess();
	}else{
	    this.initBEMemAccess();
	}
    }

    initLEMemAccess(){
	var THIS = this;

	let regionMap = this.regionMap = [], lr = 0;
	for( let i=0; i<256; ++i ){
	    regionMap[i] = regionMap[i] || 0;
	    let region = this.map.array[lr];
	    if( !region ) break;
	    let base = region.base>>>24;
	    if( base > i ) continue;
	    if( (region.base+region.length) <= (i<<24>>>0) ){
		lr++;
		i-=2;
	    }else{
		let rm = regionMap[i];
		if( !rm ){
		    regionMap[i] = {
			map:{},
			array:[region],
			mask:0
		    };
		}else{
		    rm.array.push(region);
		}
	    }
	}

	for( let i=0; i<256; ++i ){
	    let metaRegion = regionMap[i];
	    if( !metaRegion ) continue;
	    let arr = metaRegion.array;
	    let mask = 0;
	    let bits = arr[0].base;
	    for( let j=1; j<arr.length; ++j )
		mask |= arr[j].base ^ bits;
	    for( let j=0; j<arr.length; ++j )
		metaRegion.map[ arr[j].base & mask ] = arr[j];
	    metaRegion.mask = mask;
	}

	function getRegion( addr, type, write ){
	    
	    addr = addr>>>0;
	    var metaRegion = regionMap[ addr>>>24 ];
	    let region = metaRegion && metaRegion.map[ metaRegion.mask & addr ];
	    let reladdr = region && (addr - region.base);
	    if( region && reladdr < region.length ){

		if( type != 2 /**/ && LOG_RAM/**/ ){

		    write = (write>>>0).toString(16);
		    if(
			(LOG_RAM==1 || region.register) &&
			    (!region.register || (region.register && !region.register.mute))
		    ){
			console.log (
			    "MMU: #" + (THIS.PC-2).toString(16).padStart(8, '0') + ": " +
				(["Read ", `Write #${write} over `, "Execute "][type]) +
				" #" +
				((
				    region.uint8[ reladdr ] |
					(region.uint8[ reladdr+1 ]<<8) |
					(region.uint8[ reladdr+2 ]<<16) |
					(region.uint8[ reladdr+3 ]<<24)
				)>>>0).toString(16) +
				" @ #" +
				addr.toString(16).padStart(8, "0") + " " +
				(region.register ? '[REG]    ' + region.name + ":" + region.register.name : region.name)
			);
		    }
		    
		}
		
		return region;
		
	    }
	    
	    write = (write>>>0).toString(16);
	    console.log(
		"MMU: #" + (THIS.PC-2).toString(16) + ": " +
		    (["Read ",  `Write #${write} over `, "Execute "][type]) +
		    "VOID @ #" + addr.toString(16).padStart(8, "0")
	    );
	    return null;
	};

	this.readU32 = function( addr ){
	    addr = addr>>>0;

	    let region;

	    // if( LOG_RAM )
	    // 	region = getRegion( addr, 0 );
	    // else{
		var metaRegion = regionMap[ addr>>>24 ];
		region = metaRegion && metaRegion.map[ metaRegion.mask & addr ];
	    // }

	    if( region ){
		if( !region.read )		    
		    return region.uint32[ (addr-region.base)>>2 ];
	    
		return region.read();
	    }
	    
	    return 0;
	    
	};


	this.writeU32 = function( addr, value ){

	    addr = addr>>>0;
	    let region;

	    // if( LOG_RAM )
	    // 	region = getRegion( addr, 1, value );
	    // else{
	    	var metaRegion = regionMap[ addr>>>24 ];
	    	region = metaRegion && metaRegion.map[ metaRegion.mask & addr ];
	    // }
	    
	    if( region ){
		var rw = region.write;
		if( !rw )
		    region.uint32[ (addr-region.base)>>2 ] = value;
		else
		    region.uint32[0] = region.register.write.call(
			region.device,
			value,
			region.uint32[0],
			THIS
		    );
		// rw( value );
	    }
	    
	};	

	this.readU8 = function( addr ){

	    addr = addr>>>0;
	    let region;

	    // if( LOG_RAM )
	    // 	region = getRegion( addr, 0 );
	    // else{
	    	var metaRegion = regionMap[ addr>>>24 ];
	    	region = metaRegion && metaRegion.map[ metaRegion.mask & addr ];
	    // }

	    if( !region )
		return 0;

	    if( !region.read )		    
		return region.uint8[ addr-region.base ];
	    
	    return region.read() & (0xFF << 8*((addr-region.base)&0x3));

	};


	this.writeU8 = function( addr, value ){

	    addr = addr>>>0;
	    let region;

	    // if( LOG_RAM )
	    // 	region = getRegion( addr, 1, value );
	    // else{
	    	var metaRegion = regionMap[ addr>>>24 ];
	    	region = metaRegion && metaRegion.map[ metaRegion.mask & addr ];
	    // }

	    if( region ){

		let reladdr = addr - region.base;
		
		if( !region.write )
		    region.uint8[ reladdr ] = value;
		else{
		    let mask = ~(0xFF << 8*(reladdr&0x3));
		    region.write( region.uint8[0] & mask | (value << 8*(reladdr&0x3)) );
		}
		
	    }

	};	
	
	this.readU16 = function( addr, execAccess ){
	    addr = addr>>>0;
	    let region;

	    // if( LOG_RAM )
	    // 	region = getRegion( addr, execAccess ? 2 : 0 );
	    // else{
		var metaRegion = regionMap[ addr>>>24 ];
		region = metaRegion && metaRegion.map[ metaRegion.mask & addr ];
	    // }

	    if( region ){
		    
		let reladdr = addr - region.base;
		
		if( !region.read )		    
		    return region.uint16[ reladdr>>1 ];
		
		return region.read();
		
	    }

	    return 0;
	};

	this.writeU16 = function( addr, value ){

	    let region = getRegion( addr, 1, value );

	    if( region ){

		let reladdr = addr - region.base;
		
		if( !region.write )
		    region.uint16[ reladdr>>1 ] = value;
		else{
		    let mask = ~(0xFFFF << 16*(reladdr&0x2));
		    region.write( region.uint32[0] & mask | (value << 8*(reladdr&0x2)) );
		}
		
	    }

	};	
	
    }

    initBEMemAccess(){
    }
    
    get SP(){
	return this.map.reg.uint32[13];
    }

    set SP(v){
	this.map.reg.uint32[13] = v;
    }

    get PC(){
	return this.map.reg.uint32[15];
    }

    set PC(v){
	this.map.reg.uint32[15] = v;
    }

    get IE(){
	return this.map.flags.uint8[5];
    }

    set IE( v ){
	this.map.flags.uint8[5] = v|0;
    }

    get XPSR(){
	return (this.map.flags.uint8[0] << 31) |
	    (this.map.flags.uint8[1] << 30) |
	    (this.map.flags.uint8[2] << 29) |
	    (this.map.flags.uint8[3] << 28);
    }

    burnQuota( amount ){
	this.map.flags.int32[2] -= ((amount||0)+1)*10000;
    }

    get quota(){
	return this.map.flags.int32[2];
    }

    set quota(v){
	this.map.flags.uint32[2] = v;
    }

    set XPSR( v ){
	v = v | 0;
	this.map.flags.uint8[0] = (v>>31) & 1;
	this.map.flags.uint8[1] = (v>>30) & 1;
	this.map.flags.uint8[2] = (v>>29) & 1;
	this.map.flags.uint8[3] = (v>>28) & 1;
    }

    push( value ){
	let reg = this.map.reg.uint32;
	let sram = this.map.sram;
	reg[13] -= 4;
	sram.uint32[ (reg[13]-sram.base)>>2 ] = value;
    }

    pop(){
	let reg = this.map.reg.uint32;
	let sram = this.map.sram;
	let value = sram.uint32[ (reg[13]-sram.base)>>2 ];
	reg[13] += 4;
	return value;
    }
    
    _init(){
	var THIS = this;

	this.devices.forEach( dev => {
	    if( dev.init )
		dev.init(this);
	});

	this.updateHardware = updateHardware;

	function updateHardware(){
	    let quota = THIS.quota;
	    let delta = THIS.startQuota - quota;
	    if( !delta )
		return;
	    THIS.startQuota = quota;

	    delta = (delta << 1)+1;

	    let timers = THIS.timers;
	    let ie = THIS.IE;
	    
	    for( let i=0, l=timers.length; i<l; ++i ){
		
		let int = timers[i].tick( delta, ie );
		
		if( int === undefined || !ie )
		    continue;

		THIS.IE = ie = 0;	    
		let vtor = THIS.reg.VTOR, PC = THIS.PC, reg = THIS.map.reg.uint32;

		THIS.push( THIS.XPSR );
		THIS.push( PC );
		THIS.push( reg[14] );
		THIS.push( reg[12] );
		THIS.push( reg[3] );
		THIS.push( reg[2] );
		THIS.push( reg[1] );
		THIS.push( reg[0] );

		THIS.PC = THIS.readU32(vtor+(int<<2));
		reg[14] = 0xfffffff9;

		THIS.int = int;
		if( int != 15 ){
		    console.log("Raise int ", int, PC.toString(16), THIS.PC.toString(16));
		    // LOG_RAM = 1;
		}
		
	    }
	}
	
    }

    reset(){
	if( !this.wasInit )
	    this._init();
	// console.log("RESET");
	let vtor = this.reg.VTOR;
	this.map.flags.uint8.fill(0);
	this.PC = this.readU32(vtor+0x4);
	this.SP = this.readU32(vtor+0x0);
	this.IE = 1;
	this.map.reg.uint32[14] = ~0;
	this.quota = 0;
	this.ready = true;
	this.dead = false;

	this.ticksSincePrint++;

	if( this.ticksSincePrint > 1000 ){
	    this.ticksSincePrint = 0;
	    let now = performance.now();
	    let delta = (now - this.timeSinceReboot) / 1000;
	    let cycles = this.cyclesExecuted;
	    console.log( cycles / delta );
	    this.cyclesExecuted = 0;
	    this.timeSinceReboot = now;
	}

    }

    callIAP( pc ){
	let reg = this.map.reg.uint32;

	if( pc == 0xfffffff9 ){
	    reg[0] = this.pop();
	    reg[1] = this.pop();
	    reg[2] = this.pop();
	    reg[3] = this.pop();
	    reg[12] = this.pop();
	    reg[14] = this.pop();
	    reg[15] = this.pop();
	    this.XPSR = this.pop();
	    this.IE = 1;
	    if( this.int != 15 )
		console.log( "Return from int ", reg[15].toString(16));
	    return;
	}

	let region;

	var metaRegion = this.regionMap[ pc>>>24 ];
	region = metaRegion && metaRegion.map[ metaRegion.mask & pc ];

	if( !(region && region.register && region.register.execute) ){
	    this.dead = true;
	    return;
	}

	region.register.execute.call( region.device, this, reg[0], reg[1], reg[2], reg[3] );
	reg[15] = reg[14];

    }

    runBlock(){
	
	let pc = this.PC;
	
	let bb = buildBlock.blockBits;
	let blockAddr = pc >> bb;
	let block = this.blocks[ blockAddr ];
	if( !block ){

	    if( pc & 0xFF000000 ){
		this.callIAP( pc );
		return;
	    }
	    
	    if( pc >= this.flash.length ){
		this.dead = true;
		return;
	    }
	    
	    this.quota = 0;
	    this.building = true;
	    
	    try{
		buildBlock( this, blockAddr<<bb )
		    .then( block => {
			this.blocks[ blockAddr ] = block;
			this.building = false;
		    });
	    }catch(ex){
		console.error(ex);
		this.dead = true;
		return;
	    }
	    return;
	}
	
	if( this.quota < 0 )
	    return;

	if( window.printBlock )
	    console.log( '#' + pc.toString(16), " block: ", (pc>>bb<<bb).toString(16));
	
	if( block.debug )
	    window.LOG_RAM = block.debug;

	block();

//	if( block.debug )
//	    this.dead = true;

	return;
    }

    update(){

	if( this.building )
	    return;
	
	if( this.dead === true )
	    return;

	var flags = this.map.flags.int32;
	var reg = this.map.reg.uint32;

	flags[2] /* this.quota */ = this.tickCycles;	
	while( flags[2] /* this.quota */ > 0 && !this.dead ){
	    
	    if( !this.ready )
		this.reset();

	    this.startQuota = flags[2]; // this.quota;

	    /* * /
	    this.runBlock();
	    /*/
	    let pc = reg[15];
	
	    let bb = buildBlock.blockBits;
	    let blockAddr = pc >> bb;
	    let block = this.blocks[ blockAddr ];
	    if( block ){
		// console.log( pc.toString(16) );
		block();
	    }else{

		if( pc & 0xFF000000 ){
		    this.callIAP( pc );
		    return;
		}
		
		if( pc >= this.flash.length ){
		    this.dead = true;
		    return;
		}
		
		flags[2] /* this.quota */ = 0;
		this.building = true;
	    
		buildBlock( this, blockAddr<<bb )
		    .then( block => {
			this.blocks[ blockAddr ] = block;
			this.building = false;
		    });
	    }
	    
	    /* */

	}

	
    }

}



