import React from 'react';

import './tab.css';

let EmptyTab = () => <div>Empty</div>;
let ErrorTab = () => <div>Error</div>;

export default class Tab extends React.Component {

    constructor( props ){
	super( props );
	this.state = {
	    activeTab:props.activeTab || Object.keys(props.tabs)[0],
	    tabs:props.tabs
	};
    }

    componentWillReceiveProps(props){
	this.setState({
	    activeTab:props.activeTab || Object.keys(props.tabs)[0],
	    tabs:props.tabs
	});
    }

    onClickTab(evt, key){
	if( evt.target.className !== 'CloseButton' )
	    this.setState( state => ({activeTab: key}) );
    }

    closeTab( key ){
	this.setState( state => {
	    delete state.tabs[key];
	    if( key == state.activeTab )
		return {activeTab:Object.keys(state.tabs)[0]};
	    return {};
	});
    }

    render(){
	let props = this.props;
	let activeTab = (this.state && this.state.activeTab) || props.activeTab;
	let ActiveTab, tabProps, tabs = this.state.tabs || {};

	if( !activeTab )
	    activeTab = Object.keys(tabs)[0];

	if( !tabs || !(activeTab in tabs) ){
	    ActiveTab = EmptyTab;
	    tabProps = {};
	}else
	    ({props:tabProps, component:ActiveTab} = tabs[activeTab]);

	if( !ActiveTab )
	    ActiveTab = ErrorTab;
	
	return (
	    <div className="tabcontainer">
	      <div className="tabbar">
		{Object.keys(tabs)
		    .map( (key) => <div
					 key={key}
					 className="tabitem"
					 active={(key==activeTab).toString()}
					 onClick={evt=>this.onClickTab(evt, key)}
			  >
			      {!tabs[key].canClose?null:
			       <span className="CloseButton" onClick={_=>this.closeTab(key)} role="img" aria-label="close tab"></span>
				   }
			  <span>{tabs[key].title}</span>
			  </div>
			)
		}
	    </div>
		<ActiveTab {...tabProps} tabId={activeTab} />
		</div>
	);
    }

}



