import JSZip from 'jszip';
import pool from '../libs/Pool.js';

export default class ZipParser {
    loadGame( game, system, cb ){
	if( !game.raw || (game.rawType != "application/octet-stream" && game.rawType != "application/x-arduboy") || !(game.raw instanceof ArrayBuffer) )
	    return;

	let zip;
	JSZip.loadAsync( game.raw )
	    .then( z => {
		zip = z;
		return zip.file("info.json").async("text");
	    })
	    .then( info => {
		info = JSONparse( info );
		
		let bin = info.binaries.find( bin => (bin.device||"").toLowerCase() == game.device );

		if( !bin )
		    throw new Error("No suitable binary for platform");

		return zip.file( bin.filename ).async("text");
	    })
	    .then( file => {
		game.raw = file;
		game.rawType = "application/x-hex";
		pool.call("loadGame", game, system, cb);
	    })
	    .catch( ex => console.warn(ex) );

	function JSONparse( str ){
	    
	    str += "";
	    
	    if( str.charCodeAt(0) == 0xFEFF )
		str = str.substr(1);
	    
	    try{
		return JSON.parse(str.replace(/,(?!\s*?[{["'\w])/g, ''));
	    }catch(ex){
		return null;
	    }
	    
	}
	
    }
}




