import Controller from '../Controller.js';
import React from 'react';
import skins from '../../skins';

import './sim.css';

class PartContainer extends Controller {

    update = null;
    prevSystem = null;
    mounted = false;
    
    _update(){
	
	if( !this.prevSystem || !this.mounted )
	    return;

	requestAnimationFrame( this.update );
	
	for( let k in this.prevSystem ){
	    let obj = this.prevSystem[k];
	    if( obj.part.tick )
		obj.part.tick();
	}
	
    }

    unmountSystem(){
	if( !this.prevSystem )
	    return;
	for( let k in this.prevSystem ){
	    let obj = this.prevSystem[k];
	    let part = obj.part;
	    if( part.unmount )
		part.unmount();
	}
    }

    mountSystem( props ){
	let system = props.system;
	let container = this.refs.container;

	if( system == this.prevSystem || !container )
	    return;

	this.unmountSystem();

	this.prevSystem = system;

	while( container.children.length )
	    container.removeChild( container.firstChild );

	if( !this.update ){
	    this.update = this._update.bind(this);
	    requestAnimationFrame( this.update );
	}

	let skin = skins[props.skin] || {};
	let config = skin.config || {};
	if( config.backgroundImage ){
	    let bgi = document.createElement("img");
	    bgi.src = config.backgroundImage;
	    bgi.style.position = "absolute";
	    container.appendChild( bgi );
	}

	for( let k in system ){
	    let obj = system[k];
	    let part = obj.part;
	    if( obj.ui === undefined ){
		let ui = null;
		if( part.getUI ){
		    ui = part.getUI();
		    ui.id = k;
		}
		obj.ui = ui;
	    }
	    
	    if( obj.ui ){
		container.appendChild( obj.ui );
		let p = skin[k];
		if( p ){
		    for( let att in p ){
			let patt = p[att];
			if( patt && Array.isArray(patt) )
			    patt = patt.join(" ");

			if( att === "text" )
			    obj.ui.textContent = patt;
			else
			    obj.ui.setAttribute( att, patt );
		    }
		}
	    }

	    if( part.mount )
		part.mount();
	}
	
    }

    componentWillUnmount(){
	this.mounted = false;
	super.componentWillUnmount();
    }

    componentDidMount(){
	this.mountSystem(this.props);
	this.mounted = true;
	super.componentDidMount();
    }
    
    componentWillReceiveProps( props ){
	this.mountSystem(props);
    }
    
    render(){
	return <div ref="container" id="simcontainer"></div>;
    }

}

export default class Sim extends Controller {

    constructor( props ){
	super(props);
	this.mounted = false;
	this.state = {};
	this.prevSystem = null;
	this._initSystem( props.game, props.data );
    }

    componentWillReceiveProps( props ){
	this._initSystem( props.game, props.data );
    }

    componentDidMount(){
	super.componentDidMount();
	this._initSystem( this.props.game, this.props.data );
	this.mounted = true;
    }

    _initSystem( game, state ){

	if( state.system ){

	    if( this.mounted ){
		this.setState({system:state.system});
	    }else{
		this.state.system = state.system;
	    }
	    
	}else{

	    let bin = game, sysPromise;

	    if( !bin.raw && game.binaries )
		bin = game.binaries.find( bin => {
		    sysPromise = this.message("getSystem", bin.device, bin);
		    return !!sysPromise;
		});
	    else
		sysPromise = this.message("getSystem", game.device, game);

	    if( !bin )
		return;

	    let skin = (bin.device+"").toLowerCase();
	    if( !skins[skin] )
		skin = "custom";

	    Object.assign( state, {
		status:"loading",
		system:"pending",
		bin,
		skin
	    });

	    sysPromise.then( system => {
		
		if( this.mounted ){
		    this.setState({system});
		}else{
		    this.state.system = system;
		}
		state.system = system;


		this.message("loadGame", bin, system, status => {
		    state.status = "";
		    if( this.mounted )
			this.setState({});
		});
		
	    });
	    
	}

    }
    
    render(){

	let core = this.props.data;

	if( core.status )
	    return <div>{core.status}</div>;

	return <PartContainer system={this.state.system} skin={core.skin}/>;
    }
}




