const WIDTH = 84, HEIGHT = 48;

export default class PCD8544 {
    
    getUI(){
	if( this.canvas )
	    return this.canvas;

	let canvas = this.canvas = document.createElement("canvas");
	
	canvas.width = WIDTH;
	canvas.height = HEIGHT;

	this.ctx = canvas.getContext("2d");
        this.ctx.imageSmoothingEnabled = false;
	this.ctx.msImageSmoothingEnabled = false;

	return canvas;
    }

    constructor(){

	this.gif = null;
	this.isRecording = false;
	this.recordingSkip = 0;
	this.saver = null;

	this.fb = this.createBuffer();
	this.fbON = this.createBuffer();
	this.fbOFF = this.createBuffer();
	this.activeBuffer = this.fbON;
	this.dirty = true;
	this.fade = false;

	this.fbON.data.fill(0xFF);

	for( let i=0; i<=255; ++i ){
	    let cmd = "cmd" + i.toString(16).toUpperCase();
	    if( this[cmd] ) continue;
	    this[cmd] = (()=>{ console.log("PCD8544 stub command " + i.toString(16) ); }).bind(null, i);
	}
	
	this.reset();
	
    }   
    
    tick(){
	if( !this.dirty ) //|| (this.activeBuffer == this.fb && !(this.col == 0 && this.page == 0) ) )
	    return;

	this.dirty = false;

	if( !this.ctx )
	    return;

	this.ctx.putImageData( this.activeBuffer, 0, 0 );	
    }

    createBuffer(){
	try{
            return new ImageData(
		new Uint8ClampedArray(WIDTH*HEIGHT*4),
		WIDTH,
		HEIGHT
	    );
	}catch(e){
	    return this.ctx.createImageData(WIDTH, HEIGHT);
	}
	
    }

    reset(){
	this.mode = 0;
	this.clockDivisor = 0x80;
	this.cmd = [];
	this.colStart = 0;
	this.colEnd = WIDTH-1;
	this.pageStart = 0;
	this.pageEnd = HEIGHT/8-1;
	this.col = 0;
	this.page = 0;
	this.fb.data.fill(0xFF);
	this.inverted = 0;
	this.dirty = true;
    }

    state = function( data ){
	// console.log( "DATA: " + data.toString(16) );
	let cs = this.colStart;
	let ce = this.colEnd;
	let cd = ce - cs;
	let ps = this.pageStart;
	let pe = this.pageEnd;
	let pd = pe - ps;
	
	let x = cs + this.col;
	let y = (ps + this.page) * 8;
	let fbdata = this.fb.data;
	let iOffset = (y*WIDTH + x) * 4;
	let inverted = this.inverted;

	if( !this.fade ){
	    
	    for( let i=0; i<8; ++i, iOffset += WIDTH*4 ){
		let offset = iOffset; // ((y+i)*128 + x) * 4;
		let bit = (((data >>> i) & 1) ^ inverted) * 0xFF ;
		// fbdata[ offset++ ] = bit;
		// fbdata[ offset++ ] = bit;
		// fbdata[ offset++ ] = bit;
		fbdata[ offset+3 ] = bit;
	    }
	    
	}else{

	    for( let i=0; i<8; ++i, iOffset += WIDTH*4 ){
		let offset = iOffset; // ((y+i)*128 + x) * 4;
		let bit = (((data >>> i) & 1) ^ inverted) * 0xFF;
		if( !bit )
		    bit = fbdata[ offset+3 ] >> 1;
		fbdata[ offset+3 ] = bit;
	    }
	    
	}

	this.vblank = false;
	if( !this.V ){
	    this.col++;
	    if( this.col > cd ){
		this.col = 0;
		this.page++;
		if( this.page > pd ){
		    this.vblank = true;
		    this.page = 0;
		}
	    }
	}else{
	    this.page++;
	    if( this.page > pd ){
		this.page = 0;
		this.col++;
		if( this.col > cd ){
		    this.vblank = true;
		    this.col = 0;
		}
	    }
	}
	this.dirty = true;
		 
    }


    pins = {

	sda:{
	    init:function(){
		this.reset();
	    },
    
	    MOSI:function( data ){
		
		if( !this.enabled )
		    return;

		if( this.mode ){ // data is a command
		    this.state( data );
		    return;
		}

		if( !data )
		    return; // NOP

		if( (data & 0xF8) == 0x20 ){ // Function set
		    this.H = data & 1;
		    this.V = (data>>1) & 1;
		    this.PD = (data>>2) & 1;
		    return;
		}

		if( !this.H ){

		    if( (data & 0xFA) == 0x08 ){
			let DE = ((data >> 1) & 2) | (data & 1);
			switch( DE ){
			case 0:
			    this.activeBuffer = this.fbOFF;
			    return;
			case 1:
			    this.activeBuffer = this.fbON;
			    return;

			case 3:
			    if( !this.inverted )
				this._invert();
			    this.inverted = true;
			case 2:
			    this.activeBuffer = this.fb;
			    return;
			    
			}
			return;
		    }

		    if( (data & 0xF8) == 0x40 ){
			this.page = data & 0x7;
			return;
		    }

		    if( (data & 0x80) == 0x80 ){
			this.col = data & 0x7F;
		    }
		    
		    return;
		}

		
	    }
	},

	res:{
	    onLowToHigh:function(){
		this.reset();
	    }
	},

	
	cs:{
	    onLowToHigh:function(){
		this.enabled = 0;
	    },
	    onHighToLow:function(){
		this.enabled = 1;
	    } 
	},

	dc:{
	    onLowToHigh:function(){
		this.mode = 1; // data
	    },
	    onHighToLow:function(){
		this.mode = 0; // command
	    } 
	}
    }

}






