export default class LPC11U6_SPI {
    constructor( id ){
	let base = id ? 0x40058000 : 0x40040000;
	this.id = id;
	let inBuffer = this.inBuffer = [];
	
	this.registers={
	    [base   ]:{ name:'CR0', write(v){
		this.dataSize = (v>>>0)&0xF;
		this.format = (v>>>4)&0x3; // 0-SPI, 1-TI, 2-Microwire
		return v;
	    } },
	    [base+4 ]:{ name:'CR1', write(v){
		this.loopBack = (v>>>0)&1;
		this.enabled =  (v>>>1)&1;
		this.slave   =  (v>>>2)&1;
		this.slaveOutputDisabled = (v>>>3)&1;
		return v;
	    } },
	    [base+8 ]:{
		name:'DR',
		read( v ){
		    if( inBuffer.length ){
			v = inBuffer.shift();
			this.SR =  (this.out.length ? 2 : 3) | (inBuffer.length ? 4 : 0);
		    }
		    return v;
		},
		write(v){
		    this.out.push( v );
		    this.SR =  (this.out.length ? 2 : 3) | (inBuffer.length ? 4 : 0);
		}
	    },
	    [base+12]:{
		name:'SR',
		read(v){
		    return v;
		},
		write:false,
		reset:0x3
	    },
	    [base+16]:{ name:'CPSR' },
	    [base+20]:{ name:'IMSC' },
	    [base+24]:{ name:'RIS', reset:0x8 },
	    [base+28]:{ name:'MIS' },
	    [base+32]:{ name:'ICR' },
	    [base+36]:{ name:'DMACR' }
	};
	
    }

    init( core ){
	let THIS=this;
	let out = core.pins[ `spi${this.id}Out` ];
	if( !out )
	    out = core.pins[ `spi${this.id}Out` ] = [];
	this.out = out;

	core.pins[`spi${this.id}In`] = {
	    push( v ){
		THIS.inBuffer.push(v);
		THIS.SR = (out.length ? 2 : 3) | 4;
	    }
	};

    }
}



