import Controller from '../Controller';
import React from 'react';
import './App.css';
import CMP from '../CMP.js';

let ntid = 1;

class App extends Controller {
    
    constructor(props){
	super(props);
	this.state = {
	    tabs:{}
	};

	let cfg = this.message("getInitialSettings");
	this.cfg = cfg;
	if( !cfg || !cfg.ram || !cfg.ram.run ){
	    let tabs = [];
	    this.message("pollRepos", tabs);
	    tabs.forEach( repo => {
		let tab = this._getRepoTab( repo );
		this.state.tabs[ tab.handle ] = tab;
	    });
	}
	 
    }

    componentDidMount(){
	
	super.componentDidMount();
	
	document.body.addEventListener("dragenter", event => {
	    event.stopPropagation();
	    event.preventDefault();
	});
	document.body.addEventListener("dragover", event => {
	    event.stopPropagation();
	    event.preventDefault();
	});
	document.body.addEventListener("drop", event => {
	    event.stopPropagation();
	    event.preventDefault();
	    this.message("onDropFile", event);
	});

	let cfg = this.cfg; // this.message("getInitialSettings");

	if( cfg && cfg.ram && cfg.ram.run )
	    this.onSelectGame( { title:"URL", url:cfg.ram.run, device:cfg.ram.device } );
	
    }

    onSelectGame( game ){
	this.setState( state => {
	    let handle = `repo:${ntid++}`;
	    let ret = {
		activeTab: handle,
		tabs: Object.assign(
		    {
			[handle]:{
			    canClose: true,
			    component: CMP.Sim,
			    title: game.title,
			    handle: handle,
			    props: {game:game, data:{}}
			}
		    },
		    state.tabs
		)
	    };
	    return ret;
	});
    }

    _getRepoTab( repo ){
	let tab = {
	    canClose: true,
	    component: CMP.Repo,
	    title: repo.title,
	    handle: `repo:${ntid++}`,
	    props: {repo:repo}
	};
	return tab;
    }

    render() {
	return <React.Fragment>
	    <CMP.Input />
	    <CMP.Tab tabs={this.state.tabs} activeTab={this.state.activeTab}/>
	</React.Fragment>;
    }

}

export default App;



