export default class CT32B1 {

    tick( delta, ie ){
	if( !(this.TCR&1) )
	    return;

	let PC = this.PC,
	    PR = this.PR;
	PC += delta;
	if( PC >= PR ){
	    PC -= PR;
	    this.TC++;
	}

	this.PC = PC;

    }

    registers = {
	0x40018000:{ name:"IR", write: (v, ov) => ov & ~v }, // Interrupt Register

	0x40018004:{
	    name:"TCR",
	    write: v => {
		if( v & 2 ){ // reset
		    this.TC = 0;
		    this.PC = 0;
		}
		return v;
	    }
	}, // Timer Control Register
	
	0x40018008:{ name:"TC", mute:true },  // Timer Counter
	0x4001800C:{ name:"PR" },  // Prescale Register
	0x40018010:{ name:"PC" },  // Prescale Counter
	0x40018014:{ name:"MCR" },   // Match Control Register
	0x40018018:{ name:"MR0" },   // Match Register 0
    }
}



