import JSZip from 'jszip';
import parseInf from './InfParser.js';

export default class ZipRepo {
    loadZipRepo( buf ){
	let ok, nok;

	let p = new Promise( (_ok, _nok ) => {
	    ok = _ok;
	    nok = _nok;
	});

	let z;
	let files = {};
	    
	JSZip.loadAsync( buf )
	    .catch( ex => nok(ex) )
	    .then( _z => {
		z = _z;
		return Promise.all(
		    Object
			.keys(z.files)
			.filter( f => !z.files[f].dir )
			.map( f => z.files[f].async("arraybuffer") )
		);
	    })
	    .then( fileList => {
		
		Object.keys(z.files)
		    .filter( f => !z.files[f].dir )
		    .forEach( (fileName, i) => {
			files[ fileName ] = fileList[i];
		    });

		let hexRefs = Object
		    .keys(z.files)
		    .map( f => z.files[f] )
		    .filter( f => !f.dir && /\.HEX$|\.BIN$/i.test(f.name) );

		let hexes = hexRefs.map( ref => files[ref.name] );
			     
		let infRefs = Object
		    .keys(z.files)
		    .map( f => z.files[f] )
		    .filter( f => !f.dir && /\.INF$|\.JSON$/i.test(f.name) );

		let infs = infRefs.map( ref => files[ref.name] );
		
		let hexIndex;
		hexIndex = hexRefs.reduce( (acc, ref, i) => {
		    acc[ ref.name.replace(/\.HEX$|\.BIN$/i, '') ] = {
			raw:hexes[i],
			rawType: "application/x-" + ref.name.split(".").pop().toLowerCase()
		    };
		    return acc;
		}, {} );

		let items = infRefs.map( (infRef, i) => {
		    let ext = infRef.name.split(".").pop();
		    let name = infRef.name.replace(/\.(INF|JSON)$/i, '');
		    let hex = hexIndex[name];
		    if( !hex ) return null;
		    hexIndex[name] = null;
		    let ret;
		    if( ext == "INF" )
			ret = parseInf(
			    name,
			    new Uint8Array(infs[i]),
			    hex.raw,
			    hex.rawType
			);
		    else{
			ret = {device:"arduboy"};
			try{
			    let td = new TextDecoder();
			    ret = JSON.parse(td.decode(infs[i]));
			}catch(ex){}
			ret = Object.assign( hex, ret );
		    }

		    return Object.assign( ret, {fs:files} );
		});

		let defaultHexDevice = (items[0] && items[0].device) || "arduboy";
		let defaultBinDevice = (items[0] && items[0].device) || "pokitto";

		for( let k in hexIndex ){
		    let hex = hexIndex[k];
		    if( hex == null ) continue;
		    items.push(
			Object.assign( hex, {
			    fs:files,
			    device: hex.rawType.indexOf("hex") == -1 ? defaultBinDevice : defaultHexDevice
			})
		    );
		}

		ok( items );
		
	    });

	return p;
    }

}



