const parsables = [
    'application/x-bin'
];

export default class HexParser {
    loadGame( game, system, cb ){
	if( !game.raw || !(game.raw instanceof ArrayBuffer) || parsables.indexOf(game.rawType) == -1 )
	    return;
	
	let raw = new Uint8Array(game.raw);
	
	if( raw.length > system.CPU.part.core.flash.length )
	    console.warn("Warning: CPU flash too small for image");

	system.CPU.part.core.flash.set( raw );
	cb();	
    }
}



