export default class Decoder{

    constructor( ops ){
	this.ops = [];
	let map = {};
	
	ops.forEach( opset => {
	    for( let k in opset )
		map[k] = opset[k];
	});
	
	for( let k in map ){
	    this.ops.push(
		this.initOp( Object.assign({encoding:k}, map[k]) )
	    );
	}
    }

    initOp( op ){
	let encoding = op.encoding;
	let mask = 0;
	let bits = 0;
	let pmask = {};
	let params = [];
	op.size = encoding.length >> 3;
	let bitCount = 31; // (op.size * 8) - 1;
	op.count = 0;

	encoding = encoding.padEnd(32, '#');

	for( let i=0; i<encoding.length; ++i ){
	    let ch = encoding[i];
	    switch( ch ){
	    case '1':
		bits |= 1 << (bitCount - i);
	    case '0':
		mask |= 1 << (bitCount - i);
		break;
	    case '#':
		break;
	    default:
		if( !(ch in pmask) ){
		    pmask[ch] = 0;
		    op[ch] = 0;
		    params.push(ch);
		}
		pmask[ch] |= 1 << (bitCount - i);
	    }
	}

	op.mask = mask;
	op.bits = bits;
	op.pmask = pmask;
	op.params = params;
	
	return op;
    }
    
    decode( bits, addr, debug ){
	let low = (bits & 0xFFFF) >>> 0;
	let high = bits >>> 16;

	bits = low << 16 | high;

	let ops = this.ops, op = null;

	for( let i=0, l=this.ops.length; i<l; ++i ){
	    op = ops[i];
	    if( op.enabled === false || op.bits !== (bits & op.mask) )
		op = null;
	    else
		break;
	}

	if( !op )	    
	    return null;

	if( op.decoderBreak )
	    (function(){ debugger; })();
	

	let bitCount = 31; // (op.size * 8) - 1;

	for( let i=0; i<op.params.length; ++i )
	    op[ op.params[i] ] = 0;

	for( let i=0; i<op.encoding.length; ++i ){
	    let bit = (bits >>> (bitCount-i)) & 1;
	    for( let j=0; j<op.params.length; ++j ){
		let p = op.params[j];
		if( (op.pmask[p] >>> (bitCount-i))&1 )
		    op[p] = (op[p]<<1) | bit;
	    }
	}

	op.count++;
	op.pc = (addr + 4) & (~1); // op.size*2;
	op.toString = core => '#' + addr.toString(16).padStart(8,'0') + " " +
	    op.name + " " +
	    op.params
	    .map( p => {
		if( core )
		switch( p ){
		case 'b':
		    let acc = [];
		    let v = op[p];
		    for( let i=0; i<=15; ++i, v>>=1 )
			if( v&1 ) acc.push(`R${i}`);
			return '{' +
			acc.join(", ") +
			'}';
		case 'm':
		case 'n':
		case 'd':
		case 't':
		    return `R${op[p]}${p}=(#${core.map.reg.uint32[op[p]].toString(16)})`;
		}
		return p + '=#' + op[p].toString(16);
	    })
	    .join(' ');

	/* * /
	if( debug )
	    console.log(`DEC (${op.count}): ` + op.toString());
	/* */    
	
	return op;
	
    };

}



