import blank from './blank.zip';
import fatfs from '../libs/fatfs';
import JSZip from 'jszip';

let blankImage = null;
let fs = null;

class ABDriver {
    sectorSize=512
    numSectors=2000
    readSectors( i, dest, cb ){
	dest.set( new Uint8Array(blankImage, i*512, dest.length) );
	cb();
    }
    writeSectors( i, data, cb ){
	new Uint8Array(blankImage, i*512, data.length ).set( data );
	cb();
    }
}

fetch(blank)
    .then( msg => msg.arrayBuffer() )
    .then( data => JSZip.loadAsync(data) )
    .then( zip => zip.file("blank.img").async("arraybuffer") )
    .then( img => {
	blankImage = img;
    });


export default class SD {

    init( id, desc, game ){
	this.enabled = false;
	this.checkCRC = true;
	this.writeAddress = null;
	this.command = 0;
	this.altCommand = false;
	this.argument = 0;
	this.state = 0;
	this.idle = true;
	this.resetCounter = 0;
	this.response = [];
	
	fs = fatfs.createFileSystem( new ABDriver() );
	this.image = new Uint8Array( blankImage );
	
	if( game && game.fs ){
	    let pending = 0;
	    let ok;
	    let promise = new Promise((_ok, _nok) => { ok=_ok; });

	    for( let file in game.fs ){
		pending++;
		this.ready = false;
		fs.writeFile( file, new Buffer(game.fs[file]), done );
	    }

	    function done(){
		pending--;
		if( !pending ){
		    ok();
		}
	    };

	    return promise;
	}

	return null;
    }

    crc16 = new Uint16Array([
	0x0000, 0x1021, 0x2042, 0x3063, 0x4084, 0x50A5, 0x60C6, 0x70E7,
	0x8108, 0x9129, 0xA14A, 0xB16B, 0xC18C, 0xD1AD, 0xE1CE, 0xF1EF,
	0x1231, 0x0210, 0x3273, 0x2252, 0x52B5, 0x4294, 0x72F7, 0x62D6,
	0x9339, 0x8318, 0xB37B, 0xA35A, 0xD3BD, 0xC39C, 0xF3FF, 0xE3DE,
	0x2462, 0x3443, 0x0420, 0x1401, 0x64E6, 0x74C7, 0x44A4, 0x5485,
	0xA56A, 0xB54B, 0x8528, 0x9509, 0xE5EE, 0xF5CF, 0xC5AC, 0xD58D,
	0x3653, 0x2672, 0x1611, 0x0630, 0x76D7, 0x66F6, 0x5695, 0x46B4,
	0xB75B, 0xA77A, 0x9719, 0x8738, 0xF7DF, 0xE7FE, 0xD79D, 0xC7BC,
	0x48C4, 0x58E5, 0x6886, 0x78A7, 0x0840, 0x1861, 0x2802, 0x3823,
	0xC9CC, 0xD9ED, 0xE98E, 0xF9AF, 0x8948, 0x9969, 0xA90A, 0xB92B,
	0x5AF5, 0x4AD4, 0x7AB7, 0x6A96, 0x1A71, 0x0A50, 0x3A33, 0x2A12,
	0xDBFD, 0xCBDC, 0xFBBF, 0xEB9E, 0x9B79, 0x8B58, 0xBB3B, 0xAB1A,
	0x6CA6, 0x7C87, 0x4CE4, 0x5CC5, 0x2C22, 0x3C03, 0x0C60, 0x1C41,
	0xEDAE, 0xFD8F, 0xCDEC, 0xDDCD, 0xAD2A, 0xBD0B, 0x8D68, 0x9D49,
	0x7E97, 0x6EB6, 0x5ED5, 0x4EF4, 0x3E13, 0x2E32, 0x1E51, 0x0E70,
	0xFF9F, 0xEFBE, 0xDFDD, 0xCFFC, 0xBF1B, 0xAF3A, 0x9F59, 0x8F78,
	0x9188, 0x81A9, 0xB1CA, 0xA1EB, 0xD10C, 0xC12D, 0xF14E, 0xE16F,
	0x1080, 0x00A1, 0x30C2, 0x20E3, 0x5004, 0x4025, 0x7046, 0x6067,
	0x83B9, 0x9398, 0xA3FB, 0xB3DA, 0xC33D, 0xD31C, 0xE37F, 0xF35E,
	0x02B1, 0x1290, 0x22F3, 0x32D2, 0x4235, 0x5214, 0x6277, 0x7256,
	0xB5EA, 0xA5CB, 0x95A8, 0x8589, 0xF56E, 0xE54F, 0xD52C, 0xC50D,
	0x34E2, 0x24C3, 0x14A0, 0x0481, 0x7466, 0x6447, 0x5424, 0x4405,
	0xA7DB, 0xB7FA, 0x8799, 0x97B8, 0xE75F, 0xF77E, 0xC71D, 0xD73C,
	0x26D3, 0x36F2, 0x0691, 0x16B0, 0x6657, 0x7676, 0x4615, 0x5634,
	0xD94C, 0xC96D, 0xF90E, 0xE92F, 0x99C8, 0x89E9, 0xB98A, 0xA9AB,
	0x5844, 0x4865, 0x7806, 0x6827, 0x18C0, 0x08E1, 0x3882, 0x28A3,
	0xCB7D, 0xDB5C, 0xEB3F, 0xFB1E, 0x8BF9, 0x9BD8, 0xABBB, 0xBB9A,
	0x4A75, 0x5A54, 0x6A37, 0x7A16, 0x0AF1, 0x1AD0, 0x2AB3, 0x3A92,
	0xFD2E, 0xED0F, 0xDD6C, 0xCD4D, 0xBDAA, 0xAD8B, 0x9DE8, 0x8DC9,
	0x7C26, 0x6C07, 0x5C64, 0x4C45, 0x3CA2, 0x2C83, 0x1CE0, 0x0CC1,
	0xEF1F, 0xFF3E, 0xCF5D, 0xDF7C, 0xAF9B, 0xBFBA, 0x8FD9, 0x9FF8,
	0x6E17, 0x7E36, 0x4E55, 0x5E74, 0x2E93, 0x3EB2, 0x0ED1, 0x1EF0
    ]);

    commands = {
	0( arg ){
	    this.idle = true;
	    this.response = [1];
	},
	8( arg ){ // detect SDv2
	    // arg:  Rsv(0)[31:12], Supply Voltage(1)[11:8], Check Pattern(0xAA)[7:0]
	    this.response = [this.idle?1:0, 0, 0, 0x01, 0xAA];
	},

	13( arg ){ // verify that the programming was successful
	    this.response = [ 0, 0 ]; // R2
	},

	16( arg ){
	    this.response = [ arg == 512 ? (this.idle?1:0) : 1<<6 ];
	},
	
	55( arg ){
	    this.altCommand = true;
	    this.response = [this.idle?1:0];
	},
	
	[0x80+41]( arg ){
	    //arg: Rsv(0)[31], HCS[30], Rsv(0)[29:0]
	    this.idle = false;
	    this.response = [0];
	},

	[0x80+42]( arg ){ // pullup resistor? Never heard of it.
	    this.response = [0];
	},
	
	58( arg ){ // check OCR bit
	    this.response = [this.idle?1:0,0x40,0x10,0,0];
	},

	59( arg ){ // enable/disable CRC checking
	    this.checkCRC = !!arg;
	    this.response = [this.idle?1:0];
	},
	
	17( arg ){ // single block read
	    console.log("Read block", arg);
	    let addr = arg*512;
	    let state = 0;
	    let CRC=0, THIS=this, crc16=this.crc16;
	    let image = this.image;
	    this.response = {
		length: 512 + 4,
		shift(){
		    this.length--;
		    switch( state ){
		    case 0:
			state++;
			return 0; // R1
		    case 1:
			state++;
			return ~1; // Data Token For 17
		    case 2:
			let ret = image[addr++];
			CRC = ((CRC << 8) ^ crc16[((CRC >> 8) ^ ret) & 0x00FF]) & 0xFFFF;

			if( this.length == 2 ){
			    state++;
			    THIS.response = [ CRC>>8, CRC&0xFF ];
			}
			
			return ret;
		    case 3:
			state++;
			return CRC >> 8;
		    case 4:
			state++;
			return CRC & 0xFF;
		    default:
			return 0;
		    }
		}
	    };
	},

	24( arg ){ // write block
	    console.log("Writing to", arg);
	    this.response = [0];
	    this.writeAddress = arg * 512;
	    this.state = 1;
	}
	
    }

    stubCommand( arg, cmd ){
	console.log( "Unknown SD command ", cmd, "arg: ", arg.toString(16) );
	this.response = [4];
    }

    pins = {

	SCK:{
	},

	CS:{
	    onLowToHigh(){
		this.enabled = false;
	    },
	    onHighToLow(){
		this.enabled = true;
	    }
	},
	
	MISO:{
	},
	
	MOSI:{
	    MOSI( b ){

		if( !this.enabled && b == 0xFF ){
		    this.resetCounter += 8;
		    if( this.resetCounter >= 74 ){
			this.state = 1;
			this.checkCRC = true;
			this.writeAddress = null;
		    }
		    this.pins.MISO.value = 0xFF;
		    return;
		}

		if( !this.enabled ) return;

		this.resetCounter = 0;

		if( this.response.length ){
		    this.pins.MISO.value = this.response.shift()|0;
		    return;
		}

		switch( this.state ){
		case 1:
		    if( b == 0xFF ) break;
		    else if( this.writeAddress !== null ){
			
			if( b == 0xFE ){
			    this.writeCount = 512; // write start
			    this.state = 12;
			    this.pins.MISO.value = 0;
			    return;
			}
			if( b == 0xFD ){
			    this.state = 1; // write terminator
			    this.pins.MISO.value = 0;
			    this.response = [0];
			    return;
			}
			
		    }
		    
		    if( b > 0x7F )
			break;

		    this.command = b & 0b111111;
		    this.argument = 0;
		    this.state++;
		    break;
		    
		case 2:
		case 3:
		case 4:
		case 5:
		    this.argument = (this.argument<<8) | b;
		    this.state++;
		    break;
		    
		case 6: // CRC
		    this.state++;
		    break;
		    
		case 7:
		    this.state = 1;

		    console.log("SDCMD", (this.altCommand ? "A" : "") + "CMD" + this.command );
		    
		    if( this.altCommand )
			this.command += 0x80;
		    this.altCommand = false;
		    
		    let command = this.commands[ this.command ] || this.stubCommand;
		    
		    command.call( this, this.argument, this.command );
		    
		    this.pins.MISO.value = this.response.shift()|0;
		    
		    return;

		case 10: // write block
		case 11:
		case 13: // crc1
		case 14: // crc2
		    this.state++;
		    this.pins.MISO.value = 0;
		    break;
		case 12:
		    this.image[ this.writeAddress++ ] = b;
		    this.writeCount--;
		    if( !this.writeCount )
			this.state++;
		    this.pins.MISO.value = 0;
		    return;
		case 15:
		    this.pins.MISO.value = 0x5;
		    this.state = 1;
		    return;
		}
		
		this.pins.MISO.value = 0xFF;
		
	    }
	}

    }
    
}




