function ALIGN4(value){
    return value & ~0x3;
}

function TEST( m, d, C, B ){
    return {
	// EQ
	0x0:[
	    m.if(
		m.i32.eqz(d.flags.Z()),
		m.nop(),
		B
	    )
	],
	
	// NE
	0x1:[
	    m.if(
		m.i32.eqz(d.flags.Z()),
		B
	    )
	],
	
	// CS
	0x2:[
	    m.if(
		d.flags.C(),
		B
	    )
	],
	
	// CC
	0x3:[
	    m.if(
		d.flags.C(),
		m.nop(),
		B
	    )
	],
	
	// MI
	0x4:[
	    m.if(
		d.flags.N(),
		B
	    )
	],
	
	// PL
	0x5:[
	    m.if(
		d.flags.N(),
		m.nop(),
		B
	    )
	],
	
	// VS
	0x6:[
	    m.if(
		d.flags.V(),
		B
	    )
	],
	
	// VC
	0x7:[
	    m.if(
		d.flags.V(),
		m.nop(),
		B
	    )
	],
	
	// HI
	0x8:[
	    m.if(
		m.i32.and(
		    d.flags.C(),
		    m.i32.eqz(d.flags.Z())
		),
		B
	    )
	],
	
	// LS
	0x9:[
	    m.if(
		m.i32.or(
		    m.i32.eqz(d.flags.C()),
		    d.flags.Z()
		),
		B
	    )	    
	],
	
	// GE
	0xA:[
	    m.if(
		m.i32.eq(
		    d.flags.N(),
		    d.flags.V()
		),
		B
	    )	    
	],
	
	// LT
	0xB:[
	    m.if(
		m.i32.eq(
		    d.flags.N(),
		    d.flags.V()
		),
		m.nop(),
		B
	    )	    
	],
	
	// GT
	0xC:[
	    m.if(
		m.i32.and(
		    m.i32.eqz(d.flags.Z()),
		    m.i32.eq(
			d.flags.N(),
			d.flags.V()
		    ),
		),
		B
	    )	    
	],
	
	// LE
	0xD:[
	    m.if(
		m.i32.and(
		    m.i32.eqz(d.flags.Z()),
		    m.i32.eq(
			d.flags.N(),
			d.flags.V()
		    ),
		),
		m.nop(),
		B
	    )	    
	],
	
	// AL
	0xE:B,
	
	// NE
	0xF:[
	]
	
    }[C];
}

function SubCarry( m, d, N, M ){
	// (NEG(a) & POS(b)) |
	// (NEG(a) & POS(c)) |
	//     (POS(b) & POS(c))
    
    return [
	d.flags.C(
	    m.i32.or(
		m.i32.and(
		    m.i32.lt_s(N, d(0)),
		    m.i32.or(
			m.i32.ge_s(M, d(0)),
			m.i32.ge_s(d.result(), d(0))
		    )
		),
		m.i32.and(
		    m.i32.ge_s(M, d(0)),
		    m.i32.ge_s(d.result(), d(0))
		)
	    )
	)
    ];
}

function SubOverflow( m, d, N, M ){

	//    (NEG(a) & POS(b) & POS(c)) |
    //        (POS(a) & NEG(b) & NEG(c));

    return [
	d.flags.V(
	    m.i32.or(
		m.i32.and(
		    m.i32.lt_s(N, d(0)),
		    m.i32.and(
			m.i32.ge_s(M, d(0)),
			m.i32.ge_s(d.result(), d(0))
		    )
		),
		m.i32.and(
		    m.i32.ge_s(N, d(0)),
		    m.i32.and(
			m.i32.lt_s(M, d(0)),
			m.i32.lt_s(d.result(), d(0))
		    )
		)
	    )
	)
    ];
    
}

function AddFlags( m, d, N, M ){
    return [		
	d.flags.V(
	    m.i32.shr_u(
		m.i32.or(
		    m.i32.and(
			m.i32.and( N, M ),
			m.i32.xor( d(~0), d.result() )
		    ),
		    m.i32.and(
			m.i32.and(
			    m.i32.xor( d(~0), N ),
			    m.i32.xor( d(~0), M )
			),
			m.i32.xor( d(~0), d.result() )
		    )
		),
		d(31)
	    )		    
	),
	
	d.flags.C(
	    m.i32.shr_u(
		m.i32.or(
		    m.i32.and(
			N,
			m.i32.or(
			    M,
			    m.i32.xor( d(~0), d.result() )
			)
		    ),
		    m.i32.and(
			M,
			m.i32.xor( d(~0), d.result() )
		    )
		),
		d(31)
	    )			
	)
    ];
}

