// import Store from './store/Forage.js';

import OnlineRepo from './repo/OnlineRepo.js';
import ZipRepo from './repo/ZipRepo.js';
import BuiltinRepo from './repo/BuiltinRepo.js';
import ZipParser from './repo/ZipParser.js';
import HexParser from './repo/HexParser.js';
import BinParser from './repo/BinParser.js';
import Normalizer from './repo/Normalizer.js';
import CloudCompiler from './compilers/CloudCompiler.js';
import boot from './react/index.js';
import registerServiceWorker from './registerServiceWorker';
import Builder from './systems/Builder.js';

import pool from './libs/Pool.js';

window.message = pool.call.bind(pool);

pool.add( new CloudCompiler() );
pool.add( new Normalizer() );
pool.add( new BuiltinRepo() );
pool.add( new ZipRepo() );
pool.add( new OnlineRepo("https://cors-anywhere.herokuapp.com/") );
pool.add( new ZipParser() );
pool.add( new HexParser() );
pool.add( new BinParser() );
pool.add( new Builder() );

let url, device;
let match = window.location.search.match(/[?&](arduboy|gamebuino|pokitto)=(.+)/);
if( match ){
    device = match[1];
    url = match[2];
    if( /^https?%.*/.test(url) )
	url = decodeURIComponent(url);
}

boot({
    proxy:"https://cors-anywhere.herokuapp.com/",
    ram:{
	skin: (window.location.search.match(/[?&]skin=([^&]+)/)||[])[1],
	run: url,
	device: device,
	debuggerEnabled:true,
	isNativeBuild: false
    }
});

registerServiceWorker();



