import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './app/App';
import pool from '../libs/Pool.js';

let cfg;

pool.add({
    getInitialSettings(){
	return cfg;
    }
});

export default ( _cfg ) => {

    cfg = _cfg;
    ReactDOM.render(<App />, document.getElementById('root'));
    
}



