import Hex from '../atcore/Hex.js';

const parsables = [
    'application/x-hex',
    'application/octet-stream; charset=utf-8',
    'text/plain; charset=utf-8'
];

export default class HexParser {
    loadGame( game, system, cb ){
	if( !game.raw || parsables.indexOf(game.rawType) == -1 )
	    return;

	let raw = game.raw;
	if( typeof game.raw == 'object' && game.raw instanceof ArrayBuffer ){
	    let td = new TextDecoder();
	    raw = td.decode(raw);
	}

	try{
	    Hex.parse( raw, system.CPU.part.core.flash );
	}catch(ex){
	    console.warn("Could not parse hex", game.raw);
	    return;
	}
	cb();	
    }
}



