	0x40048100:{ name:'PIOPORCAP0', read:true, write:false  /* R/O  POR captured PIO status 0 user dependent Table 54 */ },
	0x40048104:{ name:'PIOPORCAP1', read:true, write:false  /* R/O  POR captured PIO status 1 user dependent Table 55 */ },
	0x40048108:{ name:'PIOPORCAP2', read:true, write:false  /* R/O  POR captured PIO status 1 user dependent Table 55 */ },
	0x40048134:{ name:'IOCONCLKDIV6', read:true, write:true /* R/W  Peripheral clock 6 to the IOCON block for programmable glitch filter 0x0000 0000 Table 57 */ },
	0x40048138:{ name:'IOCONCLKDIV5', read:true, write:true /* R/W  Peripheral clock 5 to the IOCON block for programmable glitch filter 0x0000 0000 Table 57 */ },
	0x4004813C:{ name:'IOCONCLKDIV4', read:true, write:true /* R/W  Peripheral clock 4 to the IOCON block for programmable glitch filter 0x0000 0000 Table 57 */ },
	0x40048140:{ name:'IOCONCLKDIV3', read:true, write:true /* R/W  Peripheral clock 3 to the IOCON block for programmable glitch filter 0x0000 0000 Table 57 */ },
	0x40048144:{ name:'IOCONCLKDIV2', read:true, write:true /* R/W  Peripheral clock 2 to the IOCON block for programmable glitch filter 0x0000 0000 Table 57 */ },
	0x40048148:{ name:'IOCONCLKDIV1', read:true, write:true /* R/W  Peripheral clock 1 to the IOCON block for programmable glitch filter 0x0000 0000 Table 57 */ },
	0x4004814C:{ name:'IOCONCLKDIV0', read:true, write:true /* R/W  Peripheral clock 0 to the IOCON block for programmable glitch filter 0x0000 0000 Table 57 */ },
	0x40048150:{ name:'BODCTRL', read:true, write:true /* R/W  Brown-Out Detect 0 Table 58 */ },
	0x40048154:{ name:'SYSTCKCAL', read:true, write:true /* R/W System tick counter calibration */ },
	0x40048170:{ name:'IRQLATENCY', read:true, write:true, reset:0x10 /* R/W 0x170 IRQ delay. Allows trade-off between interrupt latency and determinism. 0x0000 0010 */ },
	0x40048174:{ name:'NMISRC', read:true, write:true /* R/W  NMI Source Control 0 Table 61 */ },
	0x40048178:{ name:'PINTSEL0', read:true, write:true /* R/W  GPIO Pin Interrupt Select register 0 0 Table 62 */ },
	0x4004817C:{ name:'PINTSEL1', read:true, write:true /* R/W  GPIO Pin Interrupt Select register 1 0 Table 62 */ },
	0x40048180:{ name:'PINTSEL2', read:true, write:true /* R/W  GPIO Pin Interrupt Select register 2 0 Table 62 */ },
	0x40048184:{ name:'PINTSEL3', read:true, write:true /* R/W  GPIO Pin Interrupt Select register 3 0 Table 62 */ },
	0x40048188:{ name:'PINTSEL4', read:true, write:true /* R/W  GPIO Pin Interrupt Select register 4 0 Table 62 */ },
	0x4004818C:{ name:'PINTSEL5', read:true, write:true /* R/W  GPIO Pin Interrupt Select register 5 0 Table 62 */ },
	0x40048190:{ name:'PINTSEL6', read:true, write:true /* R/W  GPIO Pin Interrupt Select register 6 0 Table 62 */ },
	0x40048194:{ name:'PINTSEL7', read:true, write:true /* R/W  GPIO Pin Interrupt Select register 7 0 Table 62 */ },
	0x40048198:{ name:'USBCLKCTRL', read:true, write:true /* R/W  USB clock control Table 63 */ },
	0x4004819C:{ name:'USBCLKST', read:true, write:false  /* R/O  USB clock status Table 64 */ },
	0x40048204:{ name:'STARTERP0', read:true, write:true /* R/W  Start logic 0 interrupt wake-up enable register 0 0 Table 65 */ },
	0x40048214:{ name:'STARTERP1', read:true, write:true /* R/W  Start logic 1 interrupt wake-up enable register 1 0 Table 66 */ },
	0x40048230:{ name:'PDSLEEPCFG', read:true, write:true /* R/W  Power-down states in deep-sleep mode Table 67 */ },
	0x40048234:{ name:'PDAWAKECFG', read:true, write:true /* R/W  Power-down states for wake-up from deep-sleep Table 68 */ },
	0x40048238:{ name:'PDRUNCFG', read:true, write:true /* R/W  Power configuration register Table 69 */ },
	0x400483F4:{ name:'DEVICE_ID', read:true, write:false /* 0x3F4 Device ID part dependent Table 70 */     },




	0xA0000000:{ name:"B0",  length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<0)) | (v<<0);   this.pins.PORT0_0  =v;    return v; }, read(){ return -!!this.pins.PORT0_0       ; } },
	0xA0000001:{ name:"B1",  length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<1)) | (v<<1);   this.pins.PORT0_1  =v;    return v; }, read(){ return -!!this.pins.PORT0_1       ; } },
	0xA0000002:{ name:"B2",  length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<2)) | (v<<2);   this.pins.PORT0_2  =v;    return v; }, read(){ return -!!this.pins.PORT0_2       ; } },
	0xA0000003:{ name:"B3",  length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<3)) | (v<<3);   this.pins.PORT0_3  =v;    return v; }, read(){ return -!!this.pins.PORT0_3       ; } },
	0xA0000004:{ name:"B4",  length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<4)) | (v<<4);   this.pins.PORT0_4  =v;    return v; }, read(){ return -!!this.pins.PORT0_4       ; } },
	0xA0000005:{ name:"B5",  length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<5)) | (v<<5);   this.pins.PORT0_5  =v;    return v; }, read(){ return -!!this.pins.PORT0_5       ; } },
	0xA0000006:{ name:"B6",  length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<6)) | (v<<6);   this.pins.PORT0_6  =v;    return v; }, read(){ return -!!this.pins.PORT0_6       ; } },
	0xA0000007:{ name:"B7",  length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<7)) | (v<<7);   this.pins.PORT0_7  =v;    return v; }, read(){ return -!!this.pins.PORT0_7       ; } },
	0xA0000008:{ name:"B8",  length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<8)) | (v<<8);   this.pins.PORT0_8  =v;    return v; }, read(){ return -!!this.pins.PORT0_8       ; } },
	0xA0000009:{ name:"B9",  length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<9)) | (v<<9);   this.pins.PORT0_9  =v;    return v; }, read(){ return -!!this.pins.PORT0_9       ; } },
	0xA000000A:{ name:"B10", length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<10)) | (v<<10);   this.pins.PORT0_10 = v;   return v; }, read(){ return -!!this.pins.PORT0_10      ; } },
	0xA000000B:{ name:"B11", length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<11)) | (v<<11);   this.pins.PORT0_11 = v;   return v; }, read(){ return -!!this.pins.PORT0_11      ; } },
	0xA000000C:{ name:"B12", length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<12)) | (v<<12);   this.pins.PORT0_12 = v;   return v; }, read(){ return -!!this.pins.PORT0_12      ; } },
	0xA000000D:{ name:"B13", length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<13)) | (v<<13);   this.pins.PORT0_13 = v;   return v; }, read(){ return -!!this.pins.PORT0_13      ; } },
	0xA000000E:{ name:"B14", length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<14)) | (v<<14);   this.pins.PORT0_14 = v;   return v; }, read(){ return -!!this.pins.PORT0_14      ; } },
	0xA000000F:{ name:"B15", length:1, write(v){ v=(!!v)>>0; this.PORT0=(this.PORT0&~(1<<15)) | (v<<15);   this.pins.PORT0_15 = v;   return v; }, read(){ return -!!this.pins.PORT0_15      ; } },
