		let PORT2 = this.PORT2 = (this.PORT2 & ~v);
		if( (PORT2>>0)&1 ) pins.PORT2_0  = (PORT2>>0)&1;
		if( (PORT2>>1)&1 ) pins.PORT2_1  = (PORT2>>1)&1;
		if( (PORT2>>2)&1 ) pins.PORT2_2  = (PORT2>>2)&1;
		if( (PORT2>>3)&1 ) pins.PORT2_3  = (PORT2>>3)&1;
