import * as systems from './*.json';
import parts from '../parts';
// import mc from './Microcard.json';
// import pok from './Pokitto.json';

export default class Builder {

    getSystem( name, game ){

	let desc = typeof name == "string" ? systems[
	    Object
		.keys(systems)
		.find( sysname => sysname.toLowerCase() == name.toLowerCase() ) 
	] : name;

	if( !desc ){
	    console.log(`System ${name} not found!`);
	    return undefined;
	}

	let out = {};
	let promises = [];
	for( let id in desc ){
	    let partName = desc[id].part || "";

	    let Part = parts[partName];
	    if( !Part ){
		if( partName[0] !== '-' )
		    console.log(`Part ${partName} not found!`);
		continue;
	    }
	    
	    let part = new Part();
	    
	    if( part.init ){
		let promise = part.init( id, desc[id], game );
		if( promise )
		    promises.push(promise);
	    }

	    out[id] = { part, desc:desc[id] };
	}

	for( let id in out ){
	    let obj = out[id];
	    obj.part.pins = Object.assign({}, obj.part.pins);
	    
	    for( let k in obj.desc ){
		let pinsrc = obj.part.pins[k];
		if( !pinsrc ) continue;
		
		let parts = obj.desc[k].split(".");
		let pintrg = out[parts[0]];
		if( !pintrg ){
		    console.log(`Cannot bind ${id} pin ${k} to ${parts[0]}`);
		    continue;
		}
		pintrg = pintrg.part;

		// console.log( id, k, obj.desc );

		if( pintrg.bindPin ){
		    pinsrc = Object.assign( {ctrl:obj.part}, pinsrc );
		    obj.part.pins[k] = pinsrc;
		    
		    pintrg.bindPin( parts[1], pinsrc );
		}else{
		    pintrg.pins[parts[1]] = Object.assign(
			{ctrl:obj.part},
			pintrg.pins[parts[1]]
		    );
		    
		    pinsrc.bindPin( k, pintrg.pins[parts[1]] );
		}
	    }
	}

	return Promise.all(promises).then( _ => out );
	
    }
}



