import React from 'react';
import pool from '../libs/Pool.js';

class Component extends React.Component {
    message( ... args ){
	return pool.call.apply(pool, args);
    }
    componentDidMount(){
	pool.add(this);
    }
    componentWillUnmount(){
	pool.remove(this);
    }
}

export default Component;



