import CPU from '../arm/cpu.js';

class PinRouter {
    constructor( pins, pin ){

	this.onLowToHigh = [];
	this.onHighToLow = [];
	this.MOSI = [];
	this.reference = [];
	this.pins = pins;
	this.pin = pin;

    }

    bind( part ){

	if( /^PIN/.test(this.pin) ){

	    let pins = this.pins, pin = this.pin;
	    
	    Object.defineProperty( part, "value", {
		set:function( v ){
		    pins[pin] = v;
		}
	    });
	    
	}else if( /^spi[0-9]+In$/.test(this.pin) ){

	    let pins = this.pins, pin = this.pin;
	    
	    Object.defineProperty( part, "value", {
		set:function( v ){
		    pins[ pin ].push(v);
		}
	    });	    
	    
	}else if( part.MOSI && /^spi[0-9]+Out$/.test(this.pin) ){
	    
	    let handler = this.pins[ this.pin ];
	    
	    if( !handler ){
		handler = {
		    listeners:[],
		    push( v ){
			let listeners = handler.listeners;
			for( let i=0, l=listeners.length; i<l; ++i )
			    listeners[i]( v );
		    }
		};
		this.pins[ this.pin ] = handler;
	    }
	    
	    handler.listeners.push( part.MOSI.bind(part.ctrl) );
	    
	}else{
	    
	    let ctrl = part.ctrl;

	    if( part.onLowToHigh )
		this.onLowToHigh.push( part.onLowToHigh.bind(ctrl) );

	    if( part.onHighToLow )
		this.onHighToLow.push( part.onHighToLow.bind(ctrl) );

	    if( part.MOSI )
		this.MOSI.push( part.MOSI.bind(ctrl) );

	    this.reference.push( part );
	    
	}
    }    

    prepare(){

	if( !this.reference.length )
	    return;

	let src = 'var value = 0', i, f;

	for( i=0; i<this.onHighToLow.length; ++i )
	    src += ',htl' + i + '=this.onHighToLow['+i+']';
	for( i=0; i<this.onLowToHigh.length; ++i )
	    src += ',lth' + i + '=this.onLowToHigh['+i+']';
	for( i=0; i<this.reference.length; ++i )
	    src += ',ref' + i + '=this.reference['+i+']';

	src += ';';
	
	src += 'return { ' + 
	    'get:function(){ return value; }, ' + 
	    'set:function(v){ ';

	if( this.onLowToHigh.length || this.onHighToLow.length ){
	    src += 'if( v !== value ){ v=value=v>>>0; ';
	    src += 'if(v){';
	    for( i=0; i<this.onLowToHigh.length; ++i )
		src += 'lth' + i + '();';
	    src += '}else{';
	    for( i=0; i<this.onHighToLow.length; ++i )
		src += 'htl' + i + '();';
	    src += '}';
	    src += '}';
	}

	for( i=0; i<this.reference.length; ++i )
	    src += 'ref' + i + '.value=v;';

	src += '}};';
	
	// console.log(src);

	f = (new Function(src));

	Object.defineProperty( this.pins, this.pin, f.call(this) );

    }

}

class CortexM0 {
    constructor(){
	this.core = new CPU();
	this.routers = {};
	this.ready = false;
    }

    bindPin( pin, part ){
	let pins = this.core.pins;
	let router = this.routers[ pin ];
	if( !router )
	    router = this.routers[pin] = new PinRouter(pins, pin);
	router.bind( part );
    }

    preparePins(){
	this.ready = true;
	for( let k in this.routers ){
	    let router = this.routers[k];
	    router.prepare();
	}
    }

    tick(){
	if( !this.ready )
	    this.preparePins();
	this.core.update();
    }
}

export default CortexM0;




