import pool from '../libs/Pool.js';

export default class Button {

    getUI(){
	if( this.ui )
	    return this.ui;

	let ui = this.ui = document.createElement("div");

	ui.addEventListener( "mousedown",  _ => this._setActive( true  ) );
	ui.addEventListener( "mouseup",    _ => this._setActive( false ) );
	ui.addEventListener( "touchstart", _ => this._setActive( true  ) );
	ui.addEventListener( "touchend",   _ => this._setActive( false ) );

	return ui;
    }

    init( id, desc ){
	let listen = desc.listen || [];

	listen.push( "Key" + id );
	
	listen.forEach( evt => {
	    this.listener["onPress" + evt] = _ => this._setActive( true );
	    this.listener["onRelease" + evt] = _ => this._setActive( false );
	});
	
    }
    
    mount(){
	pool.add(this.listener);
    }

    unmount(){
	pool.remove(this.listener);
    }

    _setActive( active ){
	this.pins.NO.value = active;
	this.pins.NC.value = !active;
    }

    listener = {};

    pins = {

	NO:{
	    init:function(){
		this.pins.NO.value = 0;
	    }
	},

	NC: {
	    init:function(){
		this.pins.NC.value = 1;
	    }
	}
    }
    
}




